  _alias:string;

  @Input()
  setAlias(v:string) {
    this._alias = v;
    if(v != ""){
      let nodeData = classObj.paletteDevice.model.findNodeDataForKey(classObj.selectNodeKey);
      if (nodeData != null) {
        // 需要加事务，否则修改完之后拖拽修改无效
        classObj.paletteDevice.startTransaction("modify node name");
        classObj.paletteDevice.model.setDataProperty(nodeData, "text", v);
        classObj.paletteDevice.commitTransaction("modify node name");
      }
    }
  }

  getAlias():string {
    return this._alias;
  }

  setLineId(lineId){
    this.lineId = lineId;
  }

  ngOnChanges(changes: SimpleChanges){
    for (let propName in changes) {
      let chng = changes[propName];
      let cur  = JSON.stringify(chng.currentValue);
      let prev = JSON.stringify(chng.previousValue);
    }
  }

