import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { Http,Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { config } from '../configuration';

declare var $: any;

@Component({
  selector: 'app-upload-dialog',
  templateUrl: './upload-dialog.component.html',
  styleUrls: ['./upload-dialog.component.css']
})
export class UploadDialogComponent implements OnInit {

  @ViewChild('uploadEl')
  private uploadElRef: ElementRef;

  private messages:string;
  private importIcon:string;
  private deleteIcon:string;

  // itemAlias需和Java后台参数名一致
  public uploader:FileUploader = new FileUploader({
    url: config.backendUrl + "/messagetype/uploadFile",
    method: "POST",
    itemAlias: "file",
    autoUpload: false
  });

  constructor(private http: Http, public dialogRef: MatDialogRef<any>,) {
    this.importIcon = config.iconPosition+"import.png";
    this.deleteIcon = config.iconPosition+"delete.png";
  }

  ngOnInit() {
    this.uploader.onCompleteAll = function () {
      alert("Upload success!")
    }
  }

  readAllMsType(){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/messagetypes/';
    return this.http.get(url, options)
      .subscribe(
        response => {
           this.messages = response.json();
          },
        err => {
          },
        () => {
          this.dialogRef.close((this.messages));
        }
      );
  }

  ngAfterViewInit() {
    // 解决两次提交同一文件时，提交不成功的问题
    this.uploader.onAfterAddingFile = (item => {
      this.uploadElRef.nativeElement.value = ''
    });
  }

  uploadFile(){
    this.uploader.uploadAll();
  }

  importFile(){
    $("#fileUpload").click();
  }

  saveInfo() {
    this.readAllMsType();
  }
}
