import {
  Component,
  OnInit,
  ComponentFactoryResolver,
  ViewChild,
  ElementRef,
  Output,
  EventEmitter
} from '@angular/core';
import {
  DynamicProtocolService
} from '../dynamicProtocol.service';
import {
  DynamicProtocolDirective
} from '../dynamicProtocol.directive';
import {
  IoComponent
} from '../io/io.component';
import {
  PlcComponent
} from '../plc/plc.component';
import {
  OpcuaComponent
} from '../opcua/opcua.component';
import {
  DeviceOperation,Ptl
} from '../deviceOpration';

declare var $: any;

declare var require: any;
const uuidv4 = require('uuid/v4');

@Component({
  selector: 'app-button-clk-evt',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.css']
})
export class ButtonComponent extends DeviceOperation implements OnInit {

  private selectedProtocol: string = "S7";
  private selectedAlgo: string = "Max";
  private operate: string = "new";
  private nodecfg: any;
  private componentIns: any = {};

  @ViewChild('mySelect')
  public mySelect: ElementRef;

  @ViewChild('mySelectAlgo')
  public mySelectAlgo: ElementRef;

  @ViewChild(DynamicProtocolDirective)
  private componentHost: DynamicProtocolDirective;

  @Output()
  updateEmitter: EventEmitter<any> = new EventEmitter();

  constructor(private componentFactoryResolver: ComponentFactoryResolver, private dynamicProtocolService: DynamicProtocolService) {
    super();
  }

  setSelectedProtocol(selectedProtocol: string) {
    this.selectedProtocol = selectedProtocol;
  }

  setSelectedAlgo(selectedAlgo: string) {
    this.selectedAlgo = selectedAlgo;
  }

  setOperate(operate: string) {
    this.operate = operate;
  }

  setNodecfg(nodecfg: string) {
    this.nodecfg = nodecfg;
  }

  /**
   * 切换时调用
   * @param obj
   */
  onProtocolChange(obj) {
    let index = obj.currentTarget.selectedIndex;
    let name = this.getProtocols()[index].getName();
    this.selectedProtocol = this.getConvertNameMap().get(name);
    this.displayProtocolCpt(this.selectedProtocol, "new", "");
  }

  onAlgoChange(obj) {
    let index = obj.currentTarget.selectedIndex;
    let name = this.getAlgos()[index].getName();
    this.selectedAlgo = name;
  }

  displayProtocolCpt(componentName: string, operation: string, nodeCfg: any) {
    // 根据名称构造不同的组件
    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(
      this.dynamicProtocolService.getComponent(componentName));

    let viewContainerRef = this.componentHost.viewContainerRef;

    viewContainerRef.clear();
    let componentRef = viewContainerRef.createComponent(componentFactory);
    this.componentIns = componentRef.instance;

    if (operation == 'new') {
      // 自动补全功能
      if (this.componentIns instanceof PlcComponent) {
        this.updateAutoCompletePlc(this.componentIns);
      } else if (this.componentIns instanceof OpcuaComponent) {
        this.updateAutoCompleteOpcua(this.componentIns);
      }
    } else if (operation == 'echo') {
      if (typeof (nodeCfg) == 'undefined' || nodeCfg == null) {
        return;
      }
      let nodeObj = nodeCfg;

      // 根据不同的设备进行初始化
      if (this.componentIns instanceof IoComponent) {
        //用于调节DIO与Digital I/O
        this.selectedProtocol = "Digital I/O";
      } else if (this.componentIns instanceof PlcComponent) {
        this.updateAutoCompletePlc(this.componentIns);
      } else if (this.componentIns instanceof OpcuaComponent) {
        this.selectedProtocol = "OPC UA";
        this.updateAutoCompleteOpcua(this.componentIns);
      }

      this.componentIns.cfgComponentInfo(nodeObj);
    }
  }

  ngOnInit() {
    this.initProtocol();
    this.initAlgos();
    this.initNameMap();

    this.displayProtocolCpt(this.selectedProtocol, this.operate, this.nodecfg);
  }

  initProtocol() {
    let p = new Ptl();
    p.setName("S7");
    p.setNo(0);
    this.getProtocols().push(p);

    p = new Ptl();
    p.setName("Digital I/O");
    p.setNo(1);
    this.getProtocols().push(p);

    p = new Ptl();
    p.setName("OPC UA");
    p.setNo(2);
    this.getProtocols().push(p);

    p = new Ptl();
    p.setName("USB");
    p.setNo(3);
    this.getProtocols().push(p);
  }

  ngAfterViewInit() {
    // 根据节点的类型，回显select
    for (var i = 0; i < this.getProtocols().length; i++) {
      if (this.getProtocols()[i].getName() == this.selectedProtocol) {
        this.mySelect.nativeElement.selectedIndex = this.getProtocols()[i].getNo();
        break;
      }
    }

    // 根据节点的类型，回显select
    for (var i = 0; i < this.getAlgos().length; i++) {
      if (this.getAlgos()[i].getName() == this.selectedAlgo) {
        this.mySelectAlgo.nativeElement.selectedIndex = this.getAlgos()[i].getNo();
        break;
      }
    }
  }

  /**
   * 保存button配置信息
   */
  saveInfo() {
    let bi = new DeviceInfo();
    let ds = this.componentIns.saveComponentInfo();
    if (typeof (ds) == "undefined" || ds == null) {
      return;
    }
    bi.setIoDeviceId(this.getIoDeviceId());
    bi.setIoDeviceName(this.getIoDeviceName());
    bi.setDisplayName(this.getAlias());
    bi.setType(this.getType());
    let tmpDs = new DataSource();
    tmpDs.setProtocol(ds.protocol);
    tmpDs.setConfig(JSON.stringify(ds));
    bi.setDataSource(tmpDs);
    bi.setPreprocessing(this.selectedAlgo);
    this.updateEmitter.emit(bi);
    alert("Save success!");
  }
}

class DataSource{
  private protocol:string;
  private config:any;

  setProtocol(protocol){
    this.protocol = protocol;
  }

  setConfig(config){
    this.config = config;
  }
}

class DeviceInfo {
  private ioDeviceId: string;
  private ioDeviceName: string;
  private type:string;
  private displayName: string;
  private dataSource: DataSource = new DataSource();
  private preprocessing: string;

  getType(){
    return this.type;
  }

  setType(type){
    this.type = type;
  }
  
  getDataSource() {
    return this.dataSource;
  }

  setDataSource(dataSource) {
    this.dataSource = dataSource;
  }

  getPreprocessing() {
    return this.preprocessing;
  }

  setPreprocessing(preprocessing) {
    this.preprocessing = preprocessing;
  }

  setAlgorithm(algorithm) {
    this.preprocessing = algorithm;
  }

  getIoDeviceId() {
    return this.ioDeviceId;
  }

  setIoDeviceId(ioDeviceId) {
    this.ioDeviceId = ioDeviceId;
  }

  getIoDeviceName() {
    return this.ioDeviceName;
  }

  setIoDeviceName(ioDeviceName) {
    this.ioDeviceName = ioDeviceName;
  }

  getDispalyName() {
    return this.displayName;
  }

  setDisplayName(displayName) {
    this.displayName = displayName;
  }
}
