export class MtCfg {
  private event: string;
  private resetTime: string;
  // trigger by I/O device
  private hintStatus: boolean;

  setEvent(event) {
    this.event = event;
  }

  getEvent() {
    return this.event;
  }

  setResetTime(resetTime) {
    this.resetTime = resetTime;
  }

  getResetTime() {
    return this.resetTime;
  }

  setHintStatus(hintStatus) {
    this.hintStatus = hintStatus;
  }

  getHintStatus() {
    return this.hintStatus;
  }
}

export class LineMtCfg {
  private lineId: string;
  private type: string;
  private mtCfg: MtCfg;
  // 勾选trigger by i/o device记录下节点id
  private ioDeviceId: string;

  setLineId(lineId) {
    this.lineId = lineId;
  }

  getLineId() {
    return this.lineId;
  }

  setType(type) {
    this.type = type;
  }

  getType() {
    return this.type;
  }

  setMtCfg(mtCfg) {
    this.mtCfg = mtCfg;
  }

  getMtCfg() {
    return this.mtCfg;
  }

  setIoDeviceId(ioDeviceId) {
    this.ioDeviceId = ioDeviceId;
  }

  getIoDeviceId() {
    return this.ioDeviceId;
  }
}

