export class Message {
  // message type自身Id
  private id: string;
  // gojs node key
  private nodeKey: string;
  private msgTypeName: string;
  private status: boolean;
  private disabled: boolean;
  private description: string;
  private inputs: string[];
  private options: string;
  private outputs: String[];
  private category: string;

  constructor() {
    this.inputs = [];
    this.outputs = [];
    this.status = false;
    this.disabled = false;
  }

  setCategory(category) {
    this.category = category;
  }

  setNodeKey(nodeKey) {
    this.nodeKey = nodeKey;
  }

  setId(id) {
    this.id = id;
  }

  setMsgTypeName(msgTypeName: string) {
    this.msgTypeName = msgTypeName;
  }

  getMsgTypeName() {
    return this.msgTypeName;
  }

  getInputs(): string[] {
    return this.inputs;
  }

  getOutputs(): String[] {
    return this.outputs;
  }

  getStatus(): boolean {
    return this.status;
  }

  setStatus(status: boolean) {
    this.status = status;
  }

  setInputs(inputs) {
    this.inputs = inputs;
  }
}

export class LineIodevice {
  private ioDeviceId: string;
  private ioDeviceName: string;
  private displayName: string;
  private type: string;
  private preprocessing: string;
  private dataSource: DataSource = new DataSource();
  private mappingTable: any;

  setIoDeviceId(ioDeviceId) {
    this.ioDeviceId = ioDeviceId;
  }

  setIoDeviceName(ioDeviceName) {
    this.ioDeviceName = ioDeviceName;
  }

  setDisplayName(displayName) {
    this.displayName = displayName;
  }

  setType(type) {
    this.type = type;
  }

  setPreprocessing(preprocessing) {
    this.preprocessing = preprocessing;
  }

  setDataSource(dataSource) {
    this.dataSource = dataSource;
  }

  setMappingTable(mappingTable) {
    this.mappingTable = mappingTable;
  }

}

