import { Component, OnInit, Inject} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Http, Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { config } from '../configuration';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { TranslateService } from "@ngx-translate/core";

declare var require: any;

@Component({
  selector: 'app-project-cfg',
  templateUrl: './project-cfg.component.html',
  styleUrls: ['./project-cfg.component.css']
})
export class ProjectCfgComponent implements OnInit {

  private title: string;
  private name: string;
  private description: string;
  private operate: string;
  private id: string;

  private plantFormGroup = new FormGroup({
    nameForm:new FormControl('', Validators.required),
    desForm:new FormControl()
  });

  constructor(public dialogRef: MatDialogRef<any>,private http: Http, @Inject(MAT_DIALOG_DATA) public data: any) {
    this.title = "Plant";
    this.operate = data.operate;
    this.name = data.name;
    this.description = data.comment;
    this.id = data.id;

    this.plantFormGroup.get('nameForm').setValue(this.name);
    this.plantFormGroup.get('desForm').setValue(this.description);
  }

  ngOnInit() {
  }

  saveInfo() {

    this.name = this.plantFormGroup.get('nameForm').value;
    this.description = this.plantFormGroup.get('desForm').value;

    let pro = new Plant();
    if(this.operate == 'new')
    {
      const uuidv4 = require('uuid/v4');
      let id = uuidv4().replace(/\-/g,"");
      pro.setPlantId(id);
    }
    else if(this.operate == 'modify')
    {
      pro.setPlantId(this.id);
    }
    pro.setName(this.name);
    pro.setDescription(this.description);

    // 保存数据到后台
    this.savePlantToDB(pro, this.operate);
  }

  savePlantToDB(plant, operation) {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/plants/' + plant.plantId + '/' + operation;
    return this.http.post(url, plant, options)
      .subscribe(
        data => {
          if(data.status == 200){
            this.dialogRef.close((plant));
          }
        },
        err => {
          alert("Failed to create!");
        },
        () => console.log('Call savePlantToDB Complete!')
      );
  }
}

export class Plant{
  private name:string;
  private lineNum:number;
  private status:number;
  private plantId:string;
  private description:string;
  constructor(){
    this.name = "";
    this.lineNum = 0;
    this.status = 0;
  }

  setName(name:string){
    this.name = name;
  }

  getName():string{
    return this.name;
  }


  setLineNum(lineNum:number){
    this.lineNum = lineNum;
  }

  getLineNum(){
    return this.lineNum;
  }

  setStatus(status:number){
    this.status = status;
  }

  setPlantId(plantId:string){
    this.plantId = plantId;
  }

  getPlantId()
  {
    return this.plantId;
  }

  setDescription(description:string){
    this.description = description;
  }

  getDescription():string{
    return this.description;
  }
}
