import { Component, Injectable} from '@angular/core';
import { IoDeviceComponent } from './io-device/io-device.component';

@Injectable()
export class DynamicIoDevicePanelService {
  private components = {
    'panel':IoDeviceComponent
  }
  constructor() { }

  getComponent(componentName) {
    return this.components[componentName];
  }
}
