import { Component, Injectable} from '@angular/core';
import { ButtonComponent } from './button/button.component';
import { ScannerComponent } from './scanner/scanner.component';
import { ChangeoverComponent } from './changeover/changeover.component';
import { PlcDeviceComponent } from './plc-device/plc-device.component';
import { ProximitySwitchComponent } from './proximity-switch/proximity-switch.component';

@Injectable()
export class DynamicComponentService {
  private components = {
    ButtonInstance:ButtonComponent,
    ScannerInstance:ScannerComponent,
    PlcDeviceInstance:PlcDeviceComponent,
    SwitchInstance:ProximitySwitchComponent,
    coMsgGroups:ChangeoverComponent
  }
  constructor() { }

  getComponent(componentName) {
    return this.components[componentName];
  }
}
