import { OnInit,Component,ViewChild,TemplateRef,AfterViewInit,ViewContainerRef} from '@angular/core';
import { ProjectService } from '../project.service';
import { InstanceCfgComponent,Instance } from '../instance-cfg/instance-cfg.component';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { Http, Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { Router,ActivatedRoute } from '@angular/router';
import { config } from '../configuration';
import { OptconfirmdialogComponent } from '../optconfirmdialog/optconfirmdialog.component';
import { HintdialogComponent } from '../hintdialog/hintdialog.component';

declare var $: any;
declare var require:any;

var classObj;

const uuidv4 = require('uuid/v4');

@Component({
  selector: 'app-instance-mgt',
  templateUrl: './instance-mgt.component.html',
  styleUrls: ['./instance-mgt.component.css']
})
export class InstanceMgtComponent implements OnInit {

  private instances: Instance[] = [];
  private createInstanceIcon: string;
  private downloadIcon:string;
  private resetIcon:string;
  private copyIcon:string;
  private editIcon:string;
  private deleteIcon:string;
  private insMap: Map<string,Instance> = new Map();
  private projectId: any;
  private projectName: any;

  constructor(private projectService: ProjectService, public dialog: MatDialog, private http: Http,
    private activeRoute: ActivatedRoute) {
    this.createInstanceIcon = config.iconPosition+"circle_add.png";
    this.downloadIcon = config.iconPosition+"download.png";
    this.resetIcon = config.iconPosition+"reset.png";
    this.copyIcon = config.iconPosition+"copy.png";
    this.editIcon = config.iconPosition+"edit.png";
    this.deleteIcon = config.iconPosition+"delete.png";
  }

  jumpInstance(obj){
    let lineName = obj.currentTarget.text;
    let lineId = obj.currentTarget.id;
    this.projectService.createInstance(lineName,lineId);
  }

  ngOnInit() {

    classObj = this;

    // 获得url传递的参数
    this.activeRoute.queryParams.subscribe(params => {
      this.instances = [];
      this.insMap.clear();

      this.projectId = params['projectId'];
      this.projectName = params['projectName'];
      this.readAllLines(this.projectId);
    });
  }

  readAllLines(plantId:string){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/' + plantId + '/plant';
    return this.http.get(url, options)
      .subscribe(
        response => {
          if(response['_body'] != "[]" && response['_body'] != ""){
           let lines = response.json();
           if(typeof(lines) != "undefined" && lines != null && lines.length > 0){
             for(let i=0; i<lines.length;i++){
              let ins = new Instance();
              ins.setPlantName(lines[i].plantName);
              ins.setPlantId(lines[i].plantId);
              ins.setDescription(lines[i].description);
              ins.setName(lines[i].name);
              ins.setLineId(lines[i].lineId);
              ins.setMtNum(lines[i].mtNum);
              this.instances.push(ins);
              this.insMap.set(lines[i].lineId,ins);
             }
           }
          }
        },
        err => {
          },
        () => console.log('Call Complete')
      );
  }

  ngAfterViewInit() {
  }

  /** 
   * 创建line instance
   */
  createInstance(){
    let dialogRef = this.dialog.open(InstanceCfgComponent, {
      data: {
        id:'',
        operate:'new',
        name:'',
        project:this.projectId, // 用于新建时默认选择project
        comment:''
      },
      hasBackdrop: true,
      disableClose: true,
      height: '400px',
      width: '400px'
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result != "" && typeof(result) != "undefined")
      {
        let ins = new Instance();
        ins.setPlantName(result.plantName);
        ins.setPlantId(result.plantId);
        ins.setDescription(result.description);
        ins.setName(result.name);
        ins.setLineId(result.lineId);

        this.instances.push(ins);
        this.insMap.set(result.lineId,ins);
      }
    });
  }

  /**
   * 修改instance配置
   * @param cfgInfo
   */
  modifyInstanceCfg(id:any){
    let ins = this.insMap.get(id);
    let dialogRef = this.dialog.open(InstanceCfgComponent, {
       data: {
         id:id,
         operate:'modify',
         name:ins.getName(),
         project:ins.getPlantId(),
         comment:ins.getDescription()
       },
       hasBackdrop: true,
       disableClose: true
     });

     dialogRef.afterClosed().subscribe(result => {
       if(result != "" && typeof(result) != "undefined"){
        var id = result.lineId;
        for(let i=0;i<this.instances.length;i++){
          let tmpId = this.instances[i].getLineId();
          if(id == tmpId){
            this.instances[i].setPlantName(result.plantName);
            this.instances[i].setPlantId(result.plantId);
            this.instances[i].setDescription(result.description);
            this.instances[i].setName(result.name);
            this.insMap.set(result.lineId,this.instances[i]);
            break;
          }
        }
       }
     });
  }

  /**
   * 删除project
   * @param id
   */
  deleteInstance(id:any){
    let dialogRef = this.dialog.open(OptconfirmdialogComponent, {
      data: {"hintMgs":"Are you sure to delete?"},
      hasBackdrop: true,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if("confirm" == result){
        this.delInsDB(id);
      }
    });
  }

  delInsDB(id:string){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/' + id;
    return this.http.delete(url, options)
      .subscribe(
        response => {
            let status = response['_body'];
            if(status == 1){
              let dialogRef = this.dialog.open(HintdialogComponent, {
                data: {"statusText": "Can not delete line binded products!"},
                hasBackdrop: true,
                disableClose: true
              });
            }
            else if(status == 0){
              classObj.insMap.delete(id);
              for(let i=0;i<classObj.instances.length;i++){
                let tmpId = classObj.instances[i].getLineId();
                if(id == tmpId){
                  classObj.instances.splice(i,1);
                  break;
                }
              }
              alert('Delete success!')
            }
        },
        err => {},
        () => {}
      );
  }

  resetInstance(id:string){
    let dialogRef = this.dialog.open(OptconfirmdialogComponent, {
      data: {"hintMgs":"Are you sure to reset?"},
      hasBackdrop: true,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if("confirm" == result){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });
        let url = config.backendUrl + '/lines/' + id +'/reset';
        return this.http.delete(url, options)
          .subscribe(
            response => {

              },
            err => {
              },
            () => {
              alert("Reset success!")
            }
          );
      }
    });


  }

  copyInstance(id: any){
    // 获得当前产线配置
    let curIns = this.insMap.get(id);
    let ins = new Instance();
    ins.setPlantName(curIns.getPlantName());
    ins.setPlantId(curIns.getPlantId());
    ins.setDescription(curIns.getDescription());
    ins.setName(curIns.getName()+"-copy");
    let uuid = uuidv4().replace(/\-/g,"");
    ins.setLineId(uuid);

    this.copyAndSaveLineAllInfo(id, ins);
  }

  copyAndSaveLineAllInfo(id:string, ins:Instance){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/' + id + '/allInfo';
    return this.http.post(url, ins, options)
      .subscribe(
        data => {
          if(data.status == 200){
            this.instances = [];
            this.insMap.clear();
            this.readAllLines(this.projectId);
            alert("Copy success!");
          }
        },
        err => {
          alert("Failed to copy!")
        },
        () => { 
        }
      );
  }

  downloadInstance(id: any){
    let curIns = this.insMap.get(id);
    let lineName = curIns.getName();

    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers});
    let url = config.backendUrl + '/lines/' + id + '/download';
    return this.http.post(url, lineName, options)
      .subscribe(
        data => {
          var file = new Blob([data.text()], { type: 'application/json' });
          var link = document.createElement("a");
          // Blob转化为链接
          link.setAttribute("href", URL.createObjectURL(file));
          link.setAttribute("download", lineName+'.json');
          link.style.visibility = 'hidden';
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        },
        err => {
          },
        () => {
          alert("Download success!");
        }
      );
  }
}
