import { Component, OnInit, ViewChild, ElementRef, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-changeover',
  templateUrl: './changeover.component.html',
  styleUrls: ['./changeover.component.css']
})
export class ChangeoverComponent implements OnInit {

  private ioDeviceId:string;
  private changeOverArr: any = new Array();
  private timeArr: any = new Array();
  private status: boolean = false;

  // 被选择中的change over变量
  private selectedCoVar: string = "ProductID";

  // 选择的时间
  private selectedTime: string  = "00:00:00";

  //private hintDisable: boolean = true;

  private hintStatus: boolean = false;

  setIoDeviceId(ioDeviceId){
    this.ioDeviceId = ioDeviceId;
  }

  setHintStatus(hintStatus){
    this.hintStatus = hintStatus;
  }

  @ViewChild('mySelectCo')
  public mySelectCo: ElementRef;

  @ViewChild('mySelectTime')
  public mySelectTime: ElementRef;

  @Output()
  coUpdateEmitter: EventEmitter<any> = new EventEmitter();

  @Output()
  coInpsUpdateEmitter: EventEmitter<any> = new EventEmitter();

  constructor() { }

  ngOnInit() {
    this.initChangeOverVar();
    this.initTimes();
  }

  initChangeOverVar(){
    let p = new SelectItem();
    p.setName("ProductID");
    p.setNo(0);
    this.changeOverArr.push(p);

    //p = new SelectItem();
    //p.setName("OrderID");
    //p.setNo(1);
    //this.changeOverArr.push(p);
  }

  changeSelect(obj){
    let selectedStatus = obj.checked;
    this.hintStatus = selectedStatus;
    //this.changeOverInpService.eventEmitCoInps.emit({'status':selectedStatus});
    this.coInpsUpdateEmitter.emit({'status':selectedStatus});
  }

  initTimes(){
    let p = new SelectItem();
    p.setName("00:00:00");
    p.setNo(0);
    this.timeArr.push(p);

    p = new SelectItem();
    p.setName("08:00:00");
    p.setNo(1);
    this.timeArr.push(p);

    p = new SelectItem();
    p.setName("12:00:00");
    p.setNo(2);
    this.timeArr.push(p);

    p = new SelectItem();
    p.setName("18:00:00");
    p.setNo(3);
    this.timeArr.push(p);
  }

  /**
   * 切换时调用
   * @param obj
   */
  onVariableChange(obj){
    let index = obj.currentTarget.selectedIndex;
    this.selectedCoVar = this.changeOverArr[index].name;
  }

  onTimeChange(obj){
    let index = obj.currentTarget.selectedIndex;
    this.selectedTime = this.timeArr[index].name;
  }

  /**
   * 保存button配置信息
   */
  saveInfo(){
    let obj = {};
    obj["ioDeviceId"] = this.ioDeviceId;
    obj["event"] = this.selectedCoVar;
    let tmp = this.status == true?this.selectedTime:"";
    obj["resetTime"] = tmp;
    obj["hintStatus"] = this.hintStatus;
    //this.changeOverService.eventEmitCoCfg.emit(obj);
    this.coUpdateEmitter.emit(obj);
  }

  ngAfterViewInit(){
    // 根据节点的类型，回显select
    for(var i=0;i<this.changeOverArr.length;i++){
      if(this.changeOverArr[i].name == this.selectedCoVar){
        this.mySelectCo.nativeElement.selectedIndex=this.changeOverArr[i].no;
        break;
      }
    }

    for(var i=0;i<this.timeArr.length;i++){
      if(this.timeArr[i].name == this.selectedTime){
        this.mySelectTime.nativeElement.selectedIndex=this.timeArr[i].no;
        break;
      }
    }
  }

  setStatus(status){
    this.status = status;
  }

  setSelectedCoVar(selectedCoVar){
    this.selectedCoVar = selectedCoVar;
  }

  setSelectedTime(selectedTime){
    this.selectedTime = selectedTime;
  }
}

class SelectItem{
  private name:string;
  private no:number;

  getName():string{
    return name;
  }

  setName(name:string){
    this.name=name;
  }

  setNo(no:number){
    this.no=no;
  }
}