import {
  Component,
  OnInit,
  ComponentFactoryResolver,
  ViewChild,
  ElementRef,
  Output,
  EventEmitter
} from '@angular/core';
import {
  DynamicProtocolService
} from '../dynamicProtocol.service';
import {
  IoComponent
} from '../io/io.component';
import {
  DynamicProtocolDirective
} from '../dynamicProtocol.directive';
import {
  PlcComponent
} from '../plc/plc.component';
import {
  OpcuaComponent
} from '../opcua/opcua.component';
import {
  DeviceOperation, Ptl
} from '../deviceOpration';

declare var $: any;

declare var require: any;
const uuidv4 = require('uuid/v4');

@Component({
  selector: 'app-scanner',
  templateUrl: './scanner.component.html',
  styleUrls: ['./scanner.component.css']
})
export class ScannerComponent extends DeviceOperation implements OnInit {

  private selectedProtocol: string = "S7";
  private selectedAlgo: string = "Max";
  private operate: string = "new";
  private nodecfg: any;

  private mappingTableObj: MappingTableObj = new MappingTableObj();

  private componentIns: any = {};

  private curAllDeviceIns = [];

  private oldKeyStr = new Map();

  @ViewChild('mySelect')
  public mySelect: ElementRef;

  @ViewChild('mySelectAlgo')
  public mySelectAlgo: ElementRef;

  @ViewChild(DynamicProtocolDirective)
  private componentHost: DynamicProtocolDirective;

  @Output()
  updateEmitter: EventEmitter<any> = new EventEmitter();

  constructor(private componentFactoryResolver: ComponentFactoryResolver, private dynamicProtocolService: DynamicProtocolService) {
    super();
  }

  setSelectedProtocol(selectedProtocol: string) {
    this.selectedProtocol = selectedProtocol;
  }

  setSelectedAlgo(selectedAlgo: string) {
    this.selectedAlgo = selectedAlgo;
  }

  setOperate(operate: string) {
    this.operate = operate;
  }

  setNodecfg(nodecfg: string) {
    this.nodecfg = nodecfg;
  }

  setMappingTableObj(mappingTableObj) {
    this.mappingTableObj = mappingTableObj;
  }

  onProtocolChange(obj) {
    let index = obj.currentTarget.selectedIndex;
    let name = this.getProtocols()[index].getName();
    this.selectedProtocol = this.getConvertNameMap().get(name);
    this.displayProtocolCpt(this.selectedProtocol, "new", "");
  }

  onAlgoChange(obj) {
    let index = obj.currentTarget.selectedIndex;
    let name = this.getAlgos()[index].getName();
    this.selectedAlgo = name;
  }

  displayProtocolCpt(componentName: string, operation: string, nodeCfg: any) {
    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(
      this.dynamicProtocolService.getComponent(componentName));

    let viewContainerRef = this.componentHost.viewContainerRef;

    viewContainerRef.clear();
    let componentRef = viewContainerRef.createComponent(componentFactory);
    this.componentIns = componentRef.instance;
    this.curAllDeviceIns = this.convertMapToArr(this.getAllDeviceIns());

    if (operation == 'new') {
      if (this.componentIns instanceof PlcComponent) {
        this.updateAutoCompletePlc(this.componentIns);
      } else if (this.componentIns instanceof OpcuaComponent) {
        this.updateAutoCompleteOpcua(this.componentIns);
      }
    } else if (operation == 'echo' || operation == 'copy') {
      if (typeof (nodeCfg) == 'undefined' || nodeCfg == null) {
        return;
      }
      let nodeObj = nodeCfg;

      this.oldKeyStr = this.getAllMappingKeys(this.mappingTableObj.mappingTable);

      if (this.componentIns instanceof IoComponent) {
        this.selectedProtocol = "Digital I/O";
      } else if (this.componentIns instanceof PlcComponent) {
        this.updateAutoCompletePlc(this.componentIns);
      } else if (this.componentIns instanceof OpcuaComponent) {
        this.selectedProtocol = "OPC UA";
        this.updateAutoCompleteOpcua(this.componentIns);
      }

      this.componentIns.cfgComponentInfo(nodeObj);
    }
  }

  getAllMappingKeys(mappingTable) {
    let m = new Map();
    let arr ;
    if(typeof(mappingTable) == "string"){
      arr = JSON.parse(mappingTable);
    }
    else{
      arr = mappingTable;
    }
    for (let i = 0; i < arr.length; i++) {
      let s = JSON.stringify(arr[i].key);
      m.set(s.substring(1, s.length - 1), 1);
    }
    return m;
  }

  convertMapToArr(allDeviceIns) {
    let tmpArr = [];
    allDeviceIns.forEach(function (value, key, map) {
      tmpArr.push(value);
    });

    return JSON.parse(JSON.stringify(tmpArr));
  }

  ngOnInit() {
    this.initProtocol();
    this.initAlgos();
    this.initNameMap();

    this.displayProtocolCpt(this.selectedProtocol, this.operate, this.nodecfg);
  }

  initProtocol() {
    let p = new Ptl();
    p.setName("S7");
    p.setNo(0);
    this.getProtocols().push(p);

    p = new Ptl();
    p.setName("Digital I/O");
    p.setNo(1);
    this.getProtocols().push(p);

    p = new Ptl();
    p.setName("OPC UA");
    p.setNo(2);
    this.getProtocols().push(p);

    p = new Ptl();
    p.setName("USB");
    p.setNo(3);
    this.getProtocols().push(p);
  }

  ngAfterViewInit() {
    for (var i = 0; i < this.getProtocols().length; i++) {
      if (this.getProtocols()[i].getName() == this.selectedProtocol) {
        this.mySelect.nativeElement.selectedIndex = this.getProtocols()[i].getNo();
        break;
      }
    }

    for (var i = 0; i < this.getAlgos().length; i++) {
      if (this.getAlgos()[i].getName() == this.selectedAlgo) {
        this.mySelectAlgo.nativeElement.selectedIndex = this.getAlgos()[i].getNo();
        break;
      }
    }
  }

  saveInfo() {
    let bi = new DeviceInfo();
    let ds = this.componentIns.saveComponentInfo();
    if (typeof (ds) == "undefined" || ds == null) {
      return;
    }
    bi.setIoDeviceId(this.getIoDeviceId());
    bi.setIoDeviceName(this.getIoDeviceName());
    bi.setDisplayName(this.getAlias());
    bi.setType(this.getType());
    let tmpDs = new DataSource();
    tmpDs.setProtocol(ds.protocol);
    tmpDs.setConfig(JSON.stringify(ds));
    bi.setDataSource(tmpDs);
    bi.setPreprocessing(this.selectedAlgo);

    let tmpMappings = this.mappingTableObj.mappingTable;
    for (let i = 0; i < tmpMappings.length; i++) {
      if (tmpMappings[i].key == "" || tmpMappings[i].value == "") {
        tmpMappings.splice(i, 1);
        i--;
      }
    }
    //let k = $.extend(true, {}, this.mappingTableObj);
    bi.setMappingTable(this.mappingTableObj);
    let flag = false;
    if (this.operate == "new" || this.operate == "copy") {
      flag = this.isMappingCfl(ds, tmpMappings);
    } else {
      for (let i = 0; i < this.mappingTableObj.mappingTable.length; i++) {
        if (typeof (this.oldKeyStr.get(this.mappingTableObj.mappingTable[i].key)) == "undefined") {
          let tt = [];
          tt.push(this.mappingTableObj.mappingTable[i]);
          if (this.isMappingCfl(ds, tt)) {
            flag = true;
            break;
          }
        }
      }
    }

    if (flag) {
      alert("The Mapping Table has overlaped!");
    } else {
      //this.deviceCfgService.eventEmitDcs.emit(bi);
      this.updateEmitter.emit(bi);
    }
  }

  isMappingCfl(dataSource, mappingsTable) {
    let tmpMap = new Map();
    let tmpDataSourceStr = JSON.stringify(dataSource);

    for (let i = 0; i < this.curAllDeviceIns.length; i++) {
      let ins = this.curAllDeviceIns[i];
      let tmpDs = ins.dataSource.config;
      let tmpDsStr ;
      if(typeof(tmpDs) != "string"){
        tmpDsStr = JSON.stringify(tmpDs);
      }
      else{
        tmpDsStr = tmpDs;
      }
      if (tmpDataSourceStr == tmpDsStr 
        && ins.mappingTable != null) {
        let o = ins.mappingTable.mappingTable;
        let ar;
        if(typeof(o) == "string"){
          ar = JSON.parse(o);
        }
        else{
          ar = o;
        }
        for (let i = 0; i < ar.length; i++) {
          tmpMap.set(ar[i].key, {});
        }
      }
    }

    for (let j = 0; j < mappingsTable.length; j++) {
      let tmpKey = mappingsTable[j].key;
      if (typeof (tmpMap.get(tmpKey)) != "undefined" && tmpKey) {
        return true;
      }
    }

    return false;
  }

  createNewMapping() {
    let id = uuidv4().replace(/\-/g,"");
    let mo = new MappingObject();
    mo.setId(id);
    this.mappingTableObj.mappingTable.push(mo);
  }

  deleteMapping(blockId) {
    for (let i = 0; i < this.mappingTableObj.mappingTable.length; i++) {
      let tmpId = this.mappingTableObj.mappingTable[i].id;
      if (blockId == tmpId) {
        this.mappingTableObj.mappingTable.splice(i, 1);
        break;
      }
    }
  }
}

class DataSource{
  private protocol:string;
  private config:any;

  setProtocol(protocol){
    this.protocol = protocol;
  }

  setConfig(config){
    this.config = config;
  }
}

class DeviceInfo {
  private ioDeviceId: string;
  private ioDeviceName: string;
  private displayName: string;
  private type:string;
  private dataSource: DataSource = new DataSource();
  private preprocessing: string;
  private mappingTable: MappingTableObj = new MappingTableObj();

  getType(){
    return this.type;
  }

  setType(type){
    this.type = type;
  }
  
  getDataSource() {
    return this.dataSource;
  }

  setDataSource(dataSource) {
    this.dataSource = dataSource;
  }

  getPreprocessing() {
    return this.preprocessing;
  }

  setPreprocessing(preprocessing) {
    this.preprocessing = preprocessing;
  }

  setMappingTable(mappingTable) {
    this.mappingTable = mappingTable;
  }

  getIoDeviceId() {
    return this.ioDeviceId;
  }

  setIoDeviceId(ioDeviceId) {
    this.ioDeviceId = ioDeviceId;
  }

  getIoDeviceName() {
    return this.ioDeviceName;
  }

  setIoDeviceName(ioDeviceName) {
    this.ioDeviceName = ioDeviceName;
  }

  getDispalyName() {
    return this.displayName;
  }

  setDisplayName(displayName) {
    this.displayName = displayName;
  }
}

class MappingTableObj {
  mappingTable: any = new Array();
  enabled: boolean = false;

  getMappingTable() {
    return this.mappingTable;
  }

  getEnabled() {
    return this.enabled;
  }

  setMappingTable(mappingTable) {
    this.mappingTable = mappingTable;
  }

  setEnabled(enabled) {
    this.enabled = enabled;
  }
}

class MappingObject {
  private id: string;
  private key: string = "";
  private value: string = "";

  setId(id) {
    this.id = id;
  }

  getId() {
    return this.id;
  }

  setKey(key) {
    this.key = key;
  }

  getKey() {
    return this.key;
  }

  setValue(value) {
    this.value = value;
  }

  getValue() {
    return this.value;
  }
}
