import { Component, OnInit, Inject} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Http, Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { config } from '../configuration';
import { FormControl, FormGroup, Validators } from '@angular/forms';


declare var require: any;

@Component({
  selector: 'app-product-cfg',
  templateUrl: './product-cfg.component.html',
  styleUrls: ['./product-cfg.component.css']
})
export class ProductCfgComponent implements OnInit {

  private title: string;
  private name: string;
  private description: string;
  private operate: string;
  private id: string;

  private productFormGroup = new FormGroup({
    nameForm:new FormControl('', Validators.required),
    desForm:new FormControl()
  });

  constructor(public dialogRef: MatDialogRef<any>, private http: Http, @Inject(MAT_DIALOG_DATA) public data: any) {
    this.title = "Plant View";
    this.operate = data.operate;
    this.name = data.name;
    this.description = data.comment;
    this.id = data.id;

    this.productFormGroup.get('nameForm').setValue(this.name);
    this.productFormGroup.get('desForm').setValue(this.description);
  }

  ngOnInit() {
  }

  saveInfo() {

    this.name = this.productFormGroup.get('nameForm').value;
    this.description = this.productFormGroup.get('desForm').value;

    let pro = new Product();
    if(this.operate == 'new')
    {
      const uuidv4 = require('uuid/v4');
      let id = uuidv4().replace(/\-/g,"");
      pro.setId(id);
    }
    else if(this.operate == 'modify')
    {
      pro.setId(this.id);
    }
    pro.setName(this.name);
    pro.setDescription(this.description);
    this.saveProjectToDB(pro);
  }

  saveProjectToDB(product) {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/products/' + product.id;
    return this.http.post(url, product, options)
      .subscribe(
        data => {
          if(data.status == 200){
            this.dialogRef.close((product));
          }
        },
        err => {
          alert("Failed to create!");
        },
        () => console.log('Call Complete')
      );
  }
}

export class Product{
  private name:string;
  private status:string;
  private id:string;
  private description:string;
  constructor(){
    this.name = "";
    this.status = "init";
  }

  setName(name:string){
    this.name = name;
  }

  getName():string{
    return this.name;
  }

  setStatus(status:string){
    this.status = status;
  }

  setId(id:string){
    this.id = id;
  }

  getId()
  {
    return this.id;
  }

  setDescription(description:string){
    this.description = description;
  }

  getDescription():string{
    return this.description;
  }
}
