import { NgModule }             from '@angular/core';
import { RouterModule, Routes,  } from '@angular/router';
import { InstanceComponent }   from './instance/instance.component';
import { ProjectComponent }   from './project/project.component';
import { InstanceMgtComponent }   from './instance-mgt/instance-mgt.component';
import { LineNetworkComponent }   from './line-network/line-network.component';
import { ProductMgtComponent }   from './product-mgt/product-mgt.component';

const routes: Routes = [
  { path: 'createproject', component: ProjectComponent },
  { path: 'instanceManagement', component: InstanceMgtComponent },
  { path: 'createInstance', component: InstanceComponent },
  { path: 'createLineNetwork', component: LineNetworkComponent },
  { path: 'createProduct', component: ProductMgtComponent },
  { path: '**', pathMatch: 'full', redirectTo: '/createproject' }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes,{useHash:true}) ],
  exports: [ RouterModule ]
})

export class AppRoutingModule {}
