  readStationMtsById(lineId) {
    let headers = new Headers({
      'Content-Type': 'application/json'
    });
    let options = new RequestOptions({
      headers: headers
    });
    let url = config.backendUrl + '/lines/' + lineId + '/messagetype';
    return this.http.get(url, options)
      .subscribe(
        response => {
          if (response['_body'] != "") {
            let pros = response.json();
            if (typeof (pros) != "undefined" && pros != null) {
              // 获得所有mt
              let msgTypesStr = pros.messages;
              let mtMap = new Map();
              for (let j = 0; j < msgTypesStr.length; j++) {
                mtMap.set(msgTypesStr[j].mtId, msgTypesStr[j]);
              }

              let stationMtRlas = pros.lmrs;
              for (let i = 0; i < stationMtRlas.length; i++) {
                let tmpMsg = new Message();
                tmpMsg.setId(stationMtRlas[i].mtId);
                tmpMsg.setNodeKey(stationMtRlas[i].mtUniqId);
                let tmpM = mtMap.get(stationMtRlas[i].mtId);
                tmpMsg.setMsgTypeName(tmpM.messageTypeName);
                tmpMsg.setCategory(tmpM.category);

                let mts = this.stationMtsMap.get(stationMtRlas[i].stationId);
                if (typeof (mts) == "undefined") {
                  let tmpArr = [];
                  tmpArr.push(tmpMsg);
                  this.stationMtsMap.set(stationMtRlas[i].stationId, tmpArr);
                } else {
                  mts.push(tmpMsg);
                }
              }
            }
          }
        },
        err => {
          console.log(err);
        },
        () => {}
      );
  }

