import { Component, OnInit} from '@angular/core';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';

declare var $: any;

@Component({
  selector: 'app-opcua',
  templateUrl: './opcua.component.html',
  styleUrls: ['./opcua.component.css']
})
export class OpcuaComponent implements OnInit {

  //private name: string="";
  //private alias: string="";
  private protocol: string = "OPCUA";
  private url: string="";
  private nodeId: string="";
  private cycles: any = new Array();
  private dataTypes: any = new Array();
  private selectedCycle: string;
  private selectedDataType: string;

  // url自动补全
  private urlControl: FormControl = new FormControl();
  private urlFilteredOptions: Observable<string[]>;
  private urlOptions = [];

  // address自动补全
  private nodeIdControl: FormControl = new FormControl();
  private nodeIdFilteredOptions: Observable<string[]>;
  private nodeIdOptions = [];

  constructor() {

    this.selectedCycle = "5s";
    this.selectedDataType = "INT";

    this.cycles.push("10ms");
    this.cycles.push("100ms");
    this.cycles.push("500ms");
    this.cycles.push("1s");
    this.cycles.push("5s");
    this.cycles.push("10s");
    this.cycles.push("15s");
    this.cycles.push("30s");
    this.cycles.push("1m");

    this.dataTypes.push("BOOLEAN");
    this.dataTypes.push("INT");
    this.dataTypes.push("LONG");
    this.dataTypes.push("DOUBLE");
    this.dataTypes.push("STRING");

  }

  ngOnInit() {

    this.urlFilteredOptions = this.urlControl.valueChanges
      .pipe(
        startWith(''),
        map(val => this.urlFilter(val))
      );

    this.nodeIdFilteredOptions = this.nodeIdControl.valueChanges
    .pipe(
      startWith(''),
      map(val => this.nodeIdFilter(val))
    );
  }

  ngAfterViewInit() {
    $("input.mat-input-element").css("border","");
  }

  urlFilter(val: string): string[] {
    return this.urlOptions.filter(option =>
      option.toLowerCase().includes(val.toLowerCase()));
  }

  nodeIdFilter(val: string): string[] {
    return this.nodeIdOptions.filter(option =>
      option.toLowerCase().includes(val.toLowerCase()));
  }

  onCycleChange(newVal)
  {
    this.selectedCycle = newVal;
  }

  onDataTypeChange(newVal)
  {
    this.selectedDataType = newVal;
  }

  //setName(name: string){
  //  this.name = name;
  //}

  //setAlias(alias: string){
  //  this.alias = alias;
  //}

  setProtocol(protocol:string){
    this.protocol = protocol;
  }

  setUrl(url: string){
    this.url = url;
  }

  setNodeId(nodeId:string){
    this.nodeId = nodeId;
  }

  setSelectedCycle(cycle:string){
    this.selectedCycle = cycle;
  }

  setSelectedDataType(dataType:string){
    this.selectedDataType = dataType;
  }

  getUrlOptions():string[]{
    return this.urlOptions;
  }

  getNodeIdOptions():string[]{
    return this.nodeIdOptions;
  }

  cfgComponentInfo(nodeObj){
    //this.setName(nodeObj.name);
    //this.setAlias(nodeObj.alias);
    this.setProtocol(nodeObj.protocol);
    let cfg;
    if(typeof(nodeObj.config)=="string"){
      cfg = JSON.parse(nodeObj.config);
    }
    else{
      cfg = nodeObj.config;
    }
    this.setUrl(cfg.url);
    this.setNodeId(cfg.nodeId);
    this.setSelectedCycle(cfg.cycle);
    this.setSelectedDataType(cfg.dataType);
  }

  //copyToAlias(){
  //  if(this.name != ""){
  //    this.alias = this.name;
  //  }
  //}

  saveComponentInfo():OpcuaInfo{
    if(this.judgeInputNull(this.url.trim())
       || this.judgeInputNull(this.nodeId.trim())){
        alert("All properties need to be filled!");
        return;
      }
      else{
        let oi = new OpcuaInfo();
        //oi.setName(this.name.trim());
        //oi.setAlias(this.alias.trim());
        oi.setProtocol("OPCUA");
        oi.setUrl(this.url.trim());
        oi.setNodeId(this.nodeId.trim());
        if(this.selectedCycle != null && this.selectedCycle != ""){
          oi.setCycle(this.selectedCycle);
        }
        if(this.selectedDataType != null && this.selectedDataType != ""){
          oi.setDataType(this.selectedDataType);
        }
        return oi;
      }
  }

  judgeInputNull(input:any) :boolean{
    if(typeof(input)==undefined || input == null || input.trim() == ""){
      return true;
    }
    else{
      return false;
    }
  }
}

