import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import * as go from 'gojs';

declare var $: any;

var lightText = 'whitesmoke';

const GO = go.GraphObject.make;

@Component({
  selector: 'app-line-element',
  templateUrl: './line-element.component.html',
  styleUrls: ['./line-element.component.css']
})
export class LineElementComponent implements OnInit {

  public palette: go.Palette = new go.Palette();

  @ViewChild('myPaletteDiv')
  public paletteRef: ElementRef;

  constructor() {

    this.palette.layout = GO(go.GridLayout, { 
      wrappingColumn: 1,
      alignment : go.GridLayout.Position,
      cellSize : new go.Size(1, 1),
      spacing : new go.Size(0, 2) });

    this.palette.nodeTemplate =
      GO(go.Node,
        // the main object is a Panel that surrounds a TextBlock with a rectangular Shape
        GO(go.Panel, "Auto",
          GO(go.Shape, "Rectangle",
            { fill: "#00A9C9", stroke: null },
            new go.Binding("figure", "fig")),
          GO(go.TextBlock,
            {
              font: "bold 10pt Helvetica, Arial, sans-serif",
              verticalAlignment: go.Spot.Center,
              stroke: lightText,
              margin: 5,
              stretch: go.GraphObject.Fill,
              desiredSize: new go.Size(135, 20),
              textAlign: "center",
              wrap: go.TextBlock.WrapFit,
              editable: true
            },
            new go.Binding("text").makeTwoWay())
        ),
      );  // end Node

    let nodeDataArray = [{ text: "Material Buffer",isGroup: true, category: "OfGroups",containing:[]},
    { text: "ManualWorkplace",isGroup: true, category: "OfGroups",containing:[]},
    { text: "AutoWorkplace",isGroup: true, category: "OfGroups",containing:[]},
    { text: "Conveyor",isGroup: true, category: "OfGroups",containing:[]},
    { text: "ShippingStation",isGroup: true, category: "OfGroups",containing:[]}];

    this.palette.model = new go.TreeModel(nodeDataArray);
  }

  ngOnInit() {
    this.palette.div = this.paletteRef.nativeElement;
  }

  ngAfterViewInit() {
    $(".mat-vertical-content").css("padding-bottom", "0px");
    $(".element canvas").css("left", "");

    //this.ajustPanelH();
  }

  /*ajustPanelH(){
    // 设置gojs面板的高度
    let bHeight = $("body").height();
    let stepHeight = $(".stepStyle").outerHeight();
    const titleHeight = 50.4;
    const navHeight = 64;
    $(".element").height(bHeight - navHeight - stepHeight - titleHeight - 12);
  }*/
}
