import { Component, OnInit} from '@angular/core';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';

declare var $: any;

@Component({
  selector: 'app-plc',
  templateUrl: './plc.component.html',
  styleUrls: ['./plc.component.css']
})
export class PlcComponent implements OnInit {

  //private name: string="";
  private protocol: string = "S7";
  //private alias: string="";
  private ip: string="";
  private address: string="";
  private cycles: any = new Array();
  private dataTypes: any = new Array();
  private selectedCycle: string;
  private selectedDataType: string;

  // ip自动补全
  private ipControl: FormControl = new FormControl();
  private ipFilteredOptions: Observable<string[]>;
  private ipOptions = [];

  // address自动补全
  private addressControl: FormControl = new FormControl();
  private addressFilteredOptions: Observable<string[]>;
  private addressOptions = [];

  constructor() {
    this.selectedCycle = "5s";
    this.selectedDataType = "INT";

    this.cycles.push("10ms");
    this.cycles.push("100ms");
    this.cycles.push("500ms");
    this.cycles.push("1s");
    this.cycles.push("5s");
    this.cycles.push("10s");
    this.cycles.push("15s");
    this.cycles.push("30s");
    this.cycles.push("1m");

    this.dataTypes.push("BOOLEAN");
    this.dataTypes.push("INT");
    this.dataTypes.push("LONG");
    this.dataTypes.push("DOUBLE");
    this.dataTypes.push("STRING");
  }

  ngOnInit() {

    this.ipFilteredOptions = this.ipControl.valueChanges
      .pipe(
        startWith(''),
        map(val => this.ipFilter(val))
      );

    this.addressFilteredOptions = this.addressControl.valueChanges
    .pipe(
      startWith(''),
      map(val => this.addressFilter(val))
    );
  }

  ngAfterViewInit() {
    $("input.mat-input-element").css("border","");
  }

  ipFilter(val: string): string[] {
    return this.ipOptions.filter(option =>{
      if(typeof(option) != "undefined"){
        option.toLowerCase().includes(val.toLowerCase())
      }
    })
  }

  addressFilter(val: string): string[] {
    return this.addressOptions.filter(option => {
      if(typeof(option) != "undefined"){
        option.toLowerCase().includes(val.toLowerCase());
      }
    })
  }

  onCycleChange(newVal)
  {
    this.selectedCycle = newVal;
  }

  onDataTypeChange(newVal)
  {
    this.selectedDataType = newVal;
  }

  //setName(name: string){
  //  this.name = name;
  //}

  //setAlias(alias: string){
  //  this.alias = alias;
  //}

  setProtocol(protocol:string){
    this.protocol = protocol;
  }

  setIp(ip: string){
    this.ip = ip;
  }

  setAddress(address:string){
    this.address = address;
  }

  setSelectedCycle(cycle:string){
    this.selectedCycle = cycle;
  }

  setSelectedDataType(dataType:string){
    this.selectedDataType = dataType;
  }

  getIpOptions():string[]{
    return this.ipOptions;
  }

  getAddressOptions():string[]{
    return this.addressOptions;
  }

  cfgComponentInfo(nodeObj){
    let obj;
    //this.setName(nodeObj.name);
    //this.setAlias(nodeObj.alias);
    this.setProtocol(nodeObj.protocol);
    if(typeof(nodeObj.config) == "string"){
      obj = JSON.parse(nodeObj.config);
    }
    else{
      obj = nodeObj.config;
    }
    this.setIp(obj.ip);
    this.setAddress(obj.address);
    this.setSelectedCycle(obj.cycle);
    this.setSelectedDataType(obj.dataType);
  }

  //copyToAlias(){
  //  if(this.name != ""){
  //    this.alias = this.name;
  //  }
  //}

  saveComponentInfo():PlcInfo{
    if(this.judgeInputNull(this.ip.trim())
       || this.judgeInputNull(this.address.trim())){
        alert("All properties need to be filled!");
        return;
      }
      else{
        let pi = new PlcInfo();
        //pi.setName(this.name.trim());
        //pi.setAlias(this.alias.trim());
        pi.setProtocol("S7");
        pi.setIp(this.ip.trim());
        pi.setAddress(this.address.trim());
        if(this.selectedCycle != null && this.selectedCycle != ""){
          pi.setCycle(this.selectedCycle);
        }
        if(this.selectedDataType != null && this.selectedDataType != ""){
          pi.setDataType(this.selectedDataType);
        }
        return pi;
      }
  }

  judgeInputNull(input:any) :boolean{
    if(typeof(input)==undefined || input == null || input.trim() == ""){
      return true;
    }
    else{
      return false;
    }
  }
}

