import { Component, Injectable} from '@angular/core';
import { PlcComponent } from './plc/plc.component';
import { IoComponent } from './io/io.component';
import { UsbComponent } from './usb/usb.component';
import { OpcuaComponent } from './opcua/opcua.component';

@Injectable()
export class DynamicProtocolService {
  private components = {
    'S7':PlcComponent,
    'DIO':IoComponent,
    'OPCUA':OpcuaComponent,
    'USB':UsbComponent
  }
  constructor() { }

  getComponent(componentName) {
    return this.components[componentName];
  }
}
