import {
  OnInit,
  Component,
  ViewChild,
  TemplateRef,
  AfterViewInit,
  ViewContainerRef
} from '@angular/core';
import {
  ProjectService
} from '../project.service';
import {
  ProjectCfgComponent,
  Plant
} from '../project-cfg/project-cfg.component';
import {
  MatDialog,
  MatDialogConfig
} from '@angular/material';
import {
  Http,
  Headers,
  RequestOptions,
  URLSearchParams
} from '@angular/http';
import {
  config
} from '../configuration';
import {
  HintdialogComponent
} from '../hintdialog/hintdialog.component';

declare var $: any;

@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.css']
})

export class ProjectComponent implements OnInit, AfterViewInit {
  private plants: Plant[] = [];
  private createProjectIcon: string;
  private editIcon: string;
  private deleteIcon: string;
  private proMap: Map < string, Plant > = new Map();

  constructor(private projectService: ProjectService, public dialog: MatDialog, private http: Http) {
    this.createProjectIcon = config.iconPosition + "circle_add.png";
    this.editIcon = config.iconPosition + "edit.png";
    this.deleteIcon = config.iconPosition + "delete.png";
  }

  ngOnInit() {
    this.readAllPlants();
  }

  /**
   * 读取已配置所有项目
   */
  readAllPlants() {
    let headers = new Headers({
      'Content-Type': 'application/json'
    });
    let options = new RequestOptions({
      headers: headers
    });
    let url = config.backendUrl + '/plants/';
    return this.http.get(url, options)
      .subscribe(
        response => {
          let pros = response.json();
          if (typeof (pros) != "undefined" && pros != null && pros.length > 0) {
            for (let i = 0; i < pros.length; i++) {
              let p = new Plant();
              p.setName(pros[i].name);
              p.setLineNum(pros[i].lineNum);
              p.setPlantId(pros[i].plantId);
              p.setDescription(pros[i].description);
              this.plants.push(p);
              this.proMap.set(pros[i].plantId, p);
            }
          }
        },
        err => {
          alert("Can't connect to the backend!");
        },
        () => console.log('Call readAllPlants Complete')
      );
  }

  ngAfterViewInit() {}

  /**
   * 创建plant
   */
  createPlant() {
    let dialogRef = this.dialog.open(ProjectCfgComponent, {
      data: {
        id: '',
        operate: 'new',
        name: '',
        comment: ''
      },
      hasBackdrop: true,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result != "" && typeof (result) != "undefined") {
        let pro = new Plant();
        pro.setName(result.name);
        pro.setDescription(result.description);
        pro.setPlantId(result.plantId);

        this.plants.push(pro);
        this.proMap.set(result.plantId, pro);
      }
    });
  }

  /**
   * 修改plant配置
   * @param cfgInfo
   */
  modifyPlantCfg(id: any) {
    let pro = this.proMap.get(id);
    let dialogRef = this.dialog.open(ProjectCfgComponent, {
      data: {
        id: id,
        operate: 'modify',
        name: pro.getName(),
        comment: pro.getDescription()
      },
      hasBackdrop: true,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result != "" && typeof (result) != "undefined") {
        var id = result.plantId;
        for (let i = 0; i < this.plants.length; i++) {
          let tmpId = this.plants[i].getPlantId();
          if (id == tmpId) {
            this.plants[i].setName(result.name);
            this.plants[i].setDescription(result.description);
            this.proMap.set(result.plantId, this.plants[i]);
            break;
          }
        }
      }
    });
  }

  /**
   * 删除plant
   * @param id
   */
  deletePlant(id: any) {
    let tmpPro = this.proMap.get(id);
    let tmpLineNum = tmpPro.getLineNum();
    if (tmpLineNum > 0) {
      let dialogRef = this.dialog.open(HintdialogComponent, {
        data: {
          "statusText": "Can not delete plant with lines!"
        },
        hasBackdrop: true,
        disableClose: true
      });
    } else {
      this.proMap.delete(id);
      for (let i = 0; i < this.plants.length; i++) {
        let tmpId = this.plants[i].getPlantId();
        if (id == tmpId) {
          this.plants.splice(i, 1);
          break;
        }
      }

      this.delPlant(id);
    }
  }

  delPlant(id: string) {
    let headers = new Headers({
      'Content-Type': 'application/json'
    });
    let options = new RequestOptions({
      headers: headers
    });
    let url = config.backendUrl + '/plants/' + id;
    return this.http.delete(url, options)
      .subscribe(
        response => {

        },
        err => {},
        () => console.log('Call Complete')
      );
  }

  jumpCreateLine(obj) {
    let projectId = obj.currentTarget.id;
    let projectName = obj.currentTarget.text;
    $($(".mainPanel ul li")[1]).addClass("clickColor");
    $($(".mainPanel ul li")[1]).siblings().removeClass("clickColor");
    //$(".mainPanel ul li a").get(1).click();
    // 采用事件为了改变标题
    this.projectService.eventEmit.emit({
      "projectId": projectId,
      "projectName": projectName
    });
    //this.projectService.createLine(projectId, projectName);
  }

}
