import { Component, OnInit,Input } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { config } from '../configuration';

var classObj;

@Component({
  selector: 'app-box-bind',
  templateUrl: './box-bind.component.html',
  styleUrls: ['./box-bind.component.css']
})
export class BoxBindComponent implements OnInit {

  // 保存从父组件传递过来的lineId
  private _lineId: string = "";
  private _saveFlag: string = "";
  private gwId: string = "";
  private onboardingKey: string = "";

  // 父组件传递line id获得该line下的配置
  @Input()
  set lineId(id:string){
    if(id != ""){
      this._lineId = id;
      this.readBoxCfgById(id);
    }
  }

  @Input()
  set saveFlag(str:string){
    if(str != ""){
      this._saveFlag = str;
      this.saveBoxCfg();
    }
  }

  constructor(private http: Http) {
    classObj = this;
  }

  ngOnInit() {
  }

  saveBoxCfg(){
    let boxCfg = new BoxCfg();
    boxCfg.setLineId(this._lineId);
    boxCfg.setGwId(this.gwId);
    boxCfg.setOnBoardingKey(this.onboardingKey);

    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/' + this._lineId + '/boxBinding';
    return this.http.post(url, boxCfg, options)
      .subscribe(
        data => {
        },
        err => {
        },
        () => {}
      );
  }

  readBoxCfgById(id:string){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/' + id + '/boxBinding';
    return this.http.get(url, options)
      .subscribe(
        response => {
          if(response['_body'] != ""){
            let pros = response.json();
            if(typeof(pros) != "undefined" && pros != null ){
              classObj._lineId = pros.lineId;
              classObj.gwId = pros.gwId;
              classObj.onboardingKey = pros.onboardingKey;
            }
          }
        },
        err => {
        },
        () => console.log('Call Complete')
      );
  }

}

class BoxCfg{
  private lineId: string;
  private gwId: string;
  private onboardingKey: string;

  setLineId(lineId){
    this.lineId = lineId;
  }

  getLineId(){
    return this.lineId;
  }

  setGwId(gwId){
    this.gwId = gwId;
  }

  getGwId(){
    return this.gwId;
  }

  setOnBoardingKey(onboardingKey){
    this.onboardingKey = onboardingKey;
  }

  getOnboardingKey(){
    return this.onboardingKey;
  }
}
