import { Component, AfterViewInit, CUSTOM_ELEMENTS_SCHEMA, ViewChild, ElementRef } from '@angular/core';
import { ProjectService } from './project.service';
import { Router } from '@angular/router';
import { config } from './configuration';
import { TranslateService } from "@ngx-translate/core";


declare var $:any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  private siemensLogo: string;
  private steps:string[]=[];

  // 模型div
  @ViewChild('navDiv')
  private diagramRef: ElementRef;

  constructor(public projectService: ProjectService, public translate: TranslateService) {
    this.siemensLogo = config.iconPosition+"siemens.png";

    this.translate.addLangs(['en', 'zh']);
    this.translate.setDefaultLang('en');

    const browserLang = this.translate.getBrowserLang();
    this.translate.use(browserLang.match(/en|zh/) ? browserLang : 'en');
  }

  ngAfterViewInit() {
    $($(this.diagramRef.nativeElement).find("li")[0]).addClass("clickColor");
  }

  ngOnInit() {
    this.steps.push("Plant Management");
    this.projectService.createProject();
    // 为了改变标题栏
    this.projectService.eventEmit.subscribe(obj => {
      this.steps=[];
      this.steps.push("Line Management");
      this.steps.push(obj.projectName);
      this.projectService.createLine(obj.projectId, obj.projectName);
    });
  }

  jumpHome(obj){
    this.changeStatus(obj);
    this.steps=[];
    this.steps.push("Plant Management");
    this.projectService.createProject();
  }

  jumpInstanceMgt(obj){
    this.changeStatus(obj);
    this.steps=[];
    this.steps.push("Line Management");
    // "0XALLLINES"读全部line的标识
    this.projectService.createLine("0XALLLINES","");
  }

  jumpProduct(obj){
    this.changeStatus(obj);
    this.steps=[];
    this.steps.push("Plant View");
    this.projectService.createProduct();
  }

  jumpPythonCheck(obj){
    this.changeStatus(obj);
    this.steps=[];
    this.steps.push("Python Lint Check");
    this.projectService.checkPyLint();
  }

  changeStatus(obj){
    // 不能是obj.target
    $($(obj.currentTarget).parent()[0]).addClass("clickColor");
    $($(obj.currentTarget).parent()[0]).siblings().removeClass("clickColor");
  }
}
