import { Component, OnInit } from '@angular/core';

declare var $: any;

@Component({
  selector: 'app-usb',
  templateUrl: './usb.component.html',
  styleUrls: ['./usb.component.css']
})
export class UsbComponent implements OnInit {

  //private name: string="";
  private port: string="";
  //private alias: string="";
  private cycles: any = new Array();
  private dataTypes: any = new Array();
  private selectedCycle: string;
  private selectedDataType: string;

  constructor() {

    this.selectedCycle = "5s";
    this.selectedDataType = "STRING";

    this.cycles.push("10ms");
    this.cycles.push("100ms");
    this.cycles.push("500ms");
    this.cycles.push("1s");
    this.cycles.push("5s");
    this.cycles.push("10s");
    this.cycles.push("15s");
    this.cycles.push("30s");
    this.cycles.push("1m");

    this.dataTypes.push("BOOLEAN");
    this.dataTypes.push("INT");
    this.dataTypes.push("LONG");
    this.dataTypes.push("DOUBLE");
    this.dataTypes.push("STRING");

  }

  ngOnInit() {
  }

  ngAfterViewInit() {
    $("input.mat-input-element").css("border","");
  }

  onCycleChange(newVal)
  {
    this.selectedCycle = newVal;
  }

  onDataTypeChange(newVal)
  {
    this.selectedDataType = newVal;
  }

  //setName(name: string){
  ///  this.name = name;
  //}

  ///setAlias(alias){
  //  this.alias = alias;
  //}

  setPort(port:string){
    this.port = port;
  }

  setSelectedCycle(cycle:string){
    this.selectedCycle = cycle;
  }

  setSelectedDataType(dataType:string){
    this.selectedDataType = dataType;
  }

  cfgComponentInfo(nodeObj){
    //this.setName(nodeObj.name);
    //this.setAlias(nodeObj.alias);
    let cfg;
    if(typeof(nodeObj.config)=="string"){
      cfg = JSON.parse(nodeObj.config);
    }
    else{
      cfg = nodeObj.config;
    }
    this.setPort(cfg.port);
    this.setSelectedCycle(cfg.cycle);
    this.setSelectedDataType(cfg.dataType);
  }

  //copyToAlias(){
   // if(this.name != ""){
   //   this.alias = this.name;
  //  }
  //}

  saveComponentInfo():UsbInfo{
    if(this.judgeInputNull(this.port.trim())){
        alert("All properties need to be filled!");
        return;
      }
      else{
        let ui = new UsbInfo();
        ui.setProtocol("USB");
        //ui.setName(this.name.trim());
        //ui.setAlias(this.alias.trim());
        ui.setPort(this.port.trim());
        if(this.selectedCycle != null && this.selectedCycle != ""){
          ui.setCycle(this.selectedCycle);
        }
        if(this.selectedDataType != null && this.selectedDataType != ""){
          ui.setDataType(this.selectedDataType);
        }
        return ui;
      }
  }

  judgeInputNull(input:any) :boolean{
    if(typeof(input)==undefined || input == null || input.trim() == ""){
      return true;
    }
    else{
      return false;
    }
  }
}

export class UsbInfo{
  //private name:string;
  //private alias:string;
  private port:string;
  private cycle:string;
  private protocol:string;
  private dataType:string;

  constructor(){
    //this.name="";
    //this.alias="";
    this.port="";
    this.cycle="5s";
    this.dataType="STRING";
  }

  //setName(name:string){
  //  this.name = name;
  //}

  //getName(): string{
  //  return this.name;
  //}

  //setAlias(alias){
   // this.alias=alias;
  //}

  //getAlias(){
  //  return this.alias;
  //}

  setPort(port:string){
    this.port = port;
  }

  getPort(){
    return this.port;
  }

  setCycle(cycle:string){
    this.cycle = cycle;
  }

  getCycle(){
    return this.cycle;
  }

  setDataType(dataType:string){
    this.dataType = dataType;
  }

  getDataType(){
    return this.dataType;
  }

  setProtocol(protocol:string){
    this.protocol = protocol;
  }

  getProtocol(){
    return this.protocol;
  }
}
