import { Component, OnInit, Inject} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Http, Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { config } from '../configuration';
import { FormControl, FormGroup, Validators } from '@angular/forms';

declare var require:any;

@Component({
  selector: 'app-instance-cfg',
  templateUrl: './instance-cfg.component.html',
  styleUrls: ['./instance-cfg.component.css']
})
export class InstanceCfgComponent implements OnInit {

  private title: string;
  private lineName: string;
  private projects:KV[] = [];
  private description: string;
  private operate: string;
  private id: string;
  private selectedProjectId:string;

  private lineFormGroup = new FormGroup({
    nameForm:new FormControl('', Validators.required),
    plantForm: new FormControl('', Validators.required),
    desForm:new FormControl()
  });

  constructor(public dialogRef: MatDialogRef<any>, @Inject(MAT_DIALOG_DATA) public data: any, private http: Http) {
    this.title = "Line";
    this.operate = data.operate;
    this.lineName = data.name;
    this.description = data.comment;
    this.selectedProjectId = data.project;
    this.id = data.id;

    this.lineFormGroup.get('nameForm').setValue(this.lineName);
    if(this.selectedProjectId == "0XALLLINES"){
      this.lineFormGroup.get('plantForm').setValue("");
    }
    else{
      this.lineFormGroup.get('plantForm').setValue(this.selectedProjectId);
    }
    this.lineFormGroup.get('desForm').setValue(this.description);
  }

  ngOnInit() {
    this.readAllPlants();
  }

  /**
   * 读取已配置所有项目
   */
  readAllPlants(){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/plants/';
    return this.http.get(url, options)
      .subscribe(
        response => {
           let pros = response.json();
           if(typeof(pros) != "undefined" && pros != null && pros.length > 0){
             for(let i=0; i<pros.length;i++){
              let obj = new KV();
              obj.setKey(pros[i].plantId);
              obj.setValue(pros[i].name);
              this.projects.push(obj);
             }
           }
          },
        err => {
          },
        () => console.log('Call Complete')
      );
  }

  saveInfo() {

    this.lineName = this.lineFormGroup.get('nameForm').value;
    this.selectedProjectId = this.lineFormGroup.get('plantForm').value;
    this.description = this.lineFormGroup.get('desForm').value;

     let ins = new Instance();
     if(this.operate == 'new')
     {
       const uuidv4 = require('uuid/v4');
       let id = uuidv4().replace(/\-/g,"");
       ins.setLineId(id);
     }
     else if(this.operate == 'modify')
     {
       ins.setLineId(this.id);
     }
     let pn = this.getProjectName(this.projects,this.selectedProjectId);
     ins.setPlantName(pn);
     ins.setPlantId(this.selectedProjectId);
     ins.setName(this.lineName);
     ins.setDescription(this.description);
     this.saveLineToDB(ins, this.operate);
     //this.updateProject(this.selectedProjectId);
  }

  getProjectName(projects:KV[],id:string):string{
    let projectName = "";
    for(let i=0;i<projects.length;i++){
      if(projects[i].getKey() == id){
        projectName = projects[i].getValue();
        break;
      }
    }
    return projectName;
  }

  saveLineToDB(line, operation) {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/' + line.id + '/' + operation;
    return this.http.post(url, line, options)
      .subscribe(
        data => {
          if(data.status == 200){
            this.dialogRef.close((line));
          }
        },
        err => {
          alert("Failed to create!");
        },
        () => console.log('Call Complete')
      );
  }

  /**
   * 对应project下产线数量加1
   * @param id
   */
   updateProject(projectId){
     let headers = new Headers({ 'Content-Type': 'application/json' });
     let options = new RequestOptions({ headers: headers });
     let url = config.backendUrl + '/plants/' + projectId + '/lineNum';
     return this.http.put(url, options)
       .subscribe(
         data => {

           },
         err => {
           alert(err);
           },
         () => console.log('Call Complete')
       );
   }
}

class KV{
  private key:string;
  private value:string;

  setKey(key:string){
    this.key = key;
  }

  getKey(){
    return this.key;
  }

  setValue(value:string){
    this.value = value;
  }

  getValue(){
    return this.value;
  }
}

export class Instance{
  private plantName:string;
  private plantId:string;
  private name:string;
  private mtNum:number;
  private lineId:string;
  private description:string;
  constructor(){
    this.plantName = "";
    this.plantId = "";
    this.name = "";
    this.mtNum = 0;
  }

  setPlantId(plantId:string){
    this.plantId = plantId;
  }

  getPlantId():string{
    return this.plantId;
  }

  setPlantName(plantName:string){
    this.plantName = plantName;
  }

  getPlantName():string{
    return this.plantName;
  }

  setName(name:string){
    this.name = name;
  }

  getName():string{
    return this.name;
  }

  setLineId(lineId:string){
    this.lineId = lineId;
  }

  getLineId(){
    return this.lineId;
  }

  setDescription(description:string){
    this.description = description;
  }

  getDescription():string{
    return this.description;
  }

  setMtNum(mtNum:number){
    this.mtNum = mtNum;
  }

  getMtNum():number{
    return this.mtNum;
  }
}
