import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { Http, Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { Instance } from '../instance-cfg/instance-cfg.component';
import { config } from '../configuration';
import * as go from 'gojs';

declare var $: any;

var lightText = 'whitesmoke';

const GO = go.GraphObject.make;

var prjMap;
var proInsMap;

@Component({
  selector: 'app-lines',
  templateUrl: './lines.component.html',
  styleUrls: ['./lines.component.css']
})
export class LinesComponent implements OnInit {

  public palette: go.Palette = new go.Palette();

  private projects: any = new Array();

  private nodeDataArray:any = new Array();

  @ViewChild('myPaletteDiv')
  public paletteRef: ElementRef;

  @ViewChild('mySelect')
  public mySelect: ElementRef;

  constructor(private http: Http) {

    prjMap = new Map();
    proInsMap = new Map();

    this.palette.layout = GO(go.GridLayout, { wrappingColumn: 1 });

    this.palette.nodeTemplate =
      GO(go.Node,
        // the main object is a Panel that surrounds a TextBlock with a rectangular Shape
        GO(go.Panel, "Auto",
          GO(go.Shape, "Rectangle",
            { fill: "#00A9C9", stroke: null },
            new go.Binding("figure", "fig")),
          GO(go.TextBlock,
            {
              font: "bold 10pt Helvetica, Arial, sans-serif",
              verticalAlignment: go.Spot.Center,
              stroke: lightText,
              margin: 5,
              stretch: go.GraphObject.Fill,
              desiredSize: new go.Size(135, 30),
              textAlign: "center",
              wrap: go.TextBlock.WrapFit,
              editable: false
            },
            new go.Binding("text").makeTwoWay())
        ),
      );  // end Node
  }

  ngOnInit() {

    this.palette.div = this.paletteRef.nativeElement;

    // "0XALLLINES"读全部line的标识
    this.readAllLines("0XALLLINES");
  }

  readAllLines(projectId:string){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/' + projectId + '/plant';
    return this.http.get(url, options)
      .subscribe(
        response => {
           let lines = response.json();
           if(typeof(lines) != "undefined" && lines != null && lines.length > 0){
             for(let i=0; i<lines.length;i++){
              let ins = new Instance();
              ins.setLineId(lines[i].lineId);
              ins.setPlantName(lines[i].plantName);
              ins.setName(lines[i].name);
              ins.setPlantId(lines[i].plantId);
              if(!proInsMap.has(lines[i].plantId)){
                let tmp = [];
                tmp.push(ins);
                proInsMap.set(lines[i].plantId,tmp);
              }
              else{
                proInsMap.get(lines[i].plantId).push(ins);
              }
             }
           }

          proInsMap.forEach((value, key, map) => {
            let p = new Prj();
            p.setName(value[0].getPlantName());
            p.setId(key);
            this.projects.push(p);
          });

          for(let i=0;i<this.projects.length;i++){
            this.projects[i].setNo(i);
            let arr = proInsMap.get(this.projects[i].id);
            let ls = [];
            for(let j=0;j<arr.length;j++){
              ls.push({"name":arr[j].getName(),"id":arr[j].getLineId()});
            }
            prjMap.set(i,ls);
          }
          //console.dir(prjMap);
        },
        err => {
          },
        () => {
          this.dispLines(0);
        }
      );
  }

  ngAfterViewInit() {
    $(".mat-vertical-content").css("padding-bottom", "0px");
    $(".element canvas").css("left", "");

    // 设置gojs面板的高度
    let bHeight = $("body").height();
    const titleHeight = 50.4;
    const navHeight = 64;
    $(".element").height(bHeight - navHeight - titleHeight - 22);
  }

  onProjectChange(obj)
  {
    let index = obj.currentTarget.selectedIndex;
    this.dispLines(index);
  }

  dispLines(index:number){
    // 清空数组
    this.nodeDataArray = [];

    let selectedPrjName = "";
    for(let j=0;j<this.projects.length;j++){
      if(index == this.projects[j].no){
        selectedPrjName = this.projects[j].name;
        break;
      }
    }

    let lines = prjMap.get(index);
    if(lines != null && lines.length > 0 ){
      for(let i=0;i<lines.length;i++){
        //let name = lines[i] + '(' + selectedPrjName + ')';
        this.nodeDataArray.push({ text: lines[i].name, category: selectedPrjName, id:lines[i].id, size:"150 75"});
      }
      this.palette.model = new go.TreeModel(this.nodeDataArray);
    }
  }
}

class Prj{
  private name:string;
  private id:string;
  private no:number;

  setName(name:string){
    this.name=name;
  }

  setNo(no:number){
    this.no=no;
  }

  setId(id:string){
    this.id=id;
  }
}
