import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { Http,Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { config } from '../configuration';
import { UploadDialogComponent } from '../upload-dialog/upload-dialog.component';
import { MatDialog, MatDialogConfig} from '@angular/material';
import * as go from 'gojs';

declare var $: any;
declare var require: any;

const GO = go.GraphObject.make;

var lightText = 'whitesmoke';
// 键为Mt id, 值为mt
var idMsgTypeMap;
// 按照category对message type分类，键为Mt类型，值为mt数组
var groupMessageMap;
// station绑定的message type, 键为station id, 值为mt数组
var stationMsgsBindedMap;
// 保存当前对象
var classObj;
// 
var msgTypesStr;
// 只在line上展示的message type类型
var mtOnlyLine = ["PRODUCTID","ORDERID","CHANGEOVER"];

@Component({
  selector: 'app-message-type2',
  templateUrl: './message-type2.component.html',
  styleUrls: ['./message-type2.component.css']
})
export class MessageType2Component implements OnInit {

  private createProjectIcon = config.iconPosition+"circle_add.png";
  // 前台展示
  private groupMessages:GroupMessage[];

  // station 下拉列表，当前选中station key
  private selectedStationKey:any;
  // station 下拉列表
  private elements: any = new Array();

  @ViewChild('mySelectStation')
  public mySelect: ElementRef;

  // 保存从父组件传递过来的lineId
  private _lineId:string = "";

  _stationsStr:any;

  // 父组件传递line id获得该line下的配置
  @Input()
  set lineId(id:string){
    this._lineId = id;
    if(id != ""){
      this.selectedStationKey = id;
      this.readStationMtsById(this._lineId);
    }
  }

  @Input()
  set stationsStr(stationsStr) {
    this._stationsStr = stationsStr;
    if( typeof(stationsStr) !="undefined" 
      && stationsStr != ""){
      let newMap = new Map();
        let oldMap = new Map();
        let stations = [];
        let s = JSON.parse(stationsStr);
        // 保存当前模型中的最上层节点
        for(let i=0; i< s.length;i++){
          let e = new Ele();
          e.setId(s[i].id);
          e.setName(s[i].name);
          e.setNo(i);
          newMap.set(s[i].id, e);
          stations.push(e);
        }

        // 通过比较，将旧的下拉列表中，在新模型中不存在的节点删掉，存在的节点更新
        for(let i=0;i<classObj.elements.length;i++){
          let tmpId = classObj.elements[i].getId();
          let tmpNewNode = newMap.get(tmpId);
          if(typeof(tmpNewNode) == "undefined"){
            classObj.elements.splice(i,1);
            i--;
          }
          else{
            classObj.elements[i].setName(tmpNewNode.getName());
            oldMap.set(tmpId, tmpNewNode);
          } 
        }

        // 将新模型中的新节点添加到下拉列表中
        for(let i=0; i< stations.length;i++){
          let tmpOldNode = oldMap.get(stations[i].getId());
          if(typeof(tmpOldNode) == "undefined"){
            classObj.elements.push(stations[i]);
          }
        }
    }
  }

  get stationsStr():string {
    return this._stationsStr;
  }

  @Output()
  msChange: EventEmitter<string> = new EventEmitter<string>();

  @ViewChild('uploadEl')
  private uploadElRef: ElementRef;

  constructor(private http: Http, private dialog: MatDialog) {
    classObj = this;
    this.groupMessages = [];
    idMsgTypeMap = new Map();
    groupMessageMap = new Map();
    stationMsgsBindedMap = new Map();
    msgTypesStr="";
  }

  /**
   * station 切换
   * @param obj 
   */
  onElementChange(obj)
  {
    // 获得gojs, 父group key
    let index = obj.currentTarget.selectedIndex;
    for(let j=0;j<this.elements.length;j++){
      if(index == this.elements[j].no){
        classObj.selectedStationKey = this.elements[j].id + '';
        break;
      }
    }

    // message type 回显
    this.convertStrToMsg(msgTypesStr, index);
  }

  convertStrToMsg(msgs, index){

    let hasProductID = false;
    let hasChangeOver = false;
    let hasOrderID = false;
    if(index == 0){
      let lineNodesMap = stationMsgsBindedMap.get(this._lineId);
      if(typeof(lineNodesMap) != "undefined"){
        if(lineNodesMap.has("PRODUCTID")){
          hasProductID = true;
        }
        if(lineNodesMap.has("CHANGEOVER")){
          hasChangeOver = true;
        }
        if(lineNodesMap.has("ORDERID")){
          hasOrderID = true;
        }
      }
    }

    // 清空数据
    groupMessageMap = new Map();
    classObj.groupMessages = [];
    for(let i=0; i<msgs.length;i++){
      let msgTypeName = msgs[i].messageTypeName;
      let mtId = msgs[i].mtId;
      let category = msgs[i].category;
      let inputs = msgs[i].inputs;
      // ProductID默认勾选
      msgs[i].status = false;

      let tmp = new Message();
      tmp.setMsgTypeName(msgTypeName);
      tmp.getInputs().push.apply(tmp.getInputs(),inputs);
      // 将所有message type放入内存
      idMsgTypeMap.set(mtId, tmp);

      // station不展示ProductID/OrderID/ChangeOver
      if(index == 0 || (index > 0 && !classObj.contains(mtOnlyLine, category))){
        // 针对line默认绑定ProductID
        if(index == 0){
          if(category.toUpperCase() == "PRODUCTID"){
            msgs[i].disabled = true;
            let stationMtMap = stationMsgsBindedMap.get(classObj.selectedStationKey);
            if(typeof(stationMtMap) == "undefined"){
              msgs[i].status = true;
              if(!hasProductID){
                this.changeSelectMt(mtId, category, msgs[i].status, msgTypeName, inputs);
              }
            }
            else{ // OrderID没有勾选，则勾选ProductID
              let orderMt = stationMtMap.get("PRODUCTID");
              if(typeof(orderMt) == "undefined"){
                msgs[i].status = true;
                if(!hasOrderID){
                  this.changeSelectMt(mtId, category, msgs[i].status, msgTypeName, inputs);
                }
              }
            }
          }
          else if(category.toUpperCase() == "CHANGEOVER"){
            msgs[i].disabled = true;
            msgs[i].status = true;
            if(!hasChangeOver){
              this.changeSelectMt(mtId, category, msgs[i].status, msgTypeName, inputs);
            }
          }
        }

        // 根据category对message type分组
        if(groupMessageMap.has(category)){
          groupMessageMap.get(category).push(msgs[i]);
        }
        else{
          let tmpArr = [];
          tmpArr.push(msgs[i]);
          groupMessageMap.set(category,tmpArr);
        }
      }
    }

    // map转对象数组，前台展示
    groupMessageMap.forEach((value, key, map)=>{
      let tmp = new GroupMessage();
      tmp.setCategory(key);

      // select勾选
      let cateMsgMap = stationMsgsBindedMap.get(classObj.selectedStationKey);
      if(typeof(cateMsgMap) != "undefined"){
        let selectedMsg = cateMsgMap.get(key);
        if(typeof(selectedMsg) != "undefined"){
          let selectedMsgId = selectedMsg.mtId;

          for(let i=0;i<value.length;i++){
            if(selectedMsgId == value[i].mtId){
              value[i].status = true;
              break;
            }
          }
        }
      }

      tmp.setMessages(value);
      classObj.groupMessages.push(tmp);
    });
  }

  contains(arr, val) {
    for (var i = 0; i < arr.length; i++) {
        if (arr[i] == val) {
            return true;
        }
    }
  }

  /**
   * messagetype切换勾选时
   * @param obj
   */
  changeSelect(obj){
    //this.nodeDataArray = [];
    // 获得message type的类型，如FPY
    let category = obj.source.name;
    let selectedStatus = obj.checked;
    // 获得点击的message type名称
    let msgTypeName = obj.source._elementRef.nativeElement.innerText.trim();
    let mtId = obj.source.id;

    this.changeSelectMt(mtId, category, selectedStatus, msgTypeName, "");
  }

  changeSelectMt(mtId, category, selectedStatus, msgTypeName, mtInputs){
    // 获得内存中的message type定义
    let messageTmp = idMsgTypeMap.get(mtId);
    let inputs;
    if(typeof(messageTmp) != "undefined" && messageTmp != ""){
      inputs = messageTmp.getInputs();
    }
    else{
      inputs = mtInputs;
    }

    // 设置model中key值
    const uuidv4 = require('uuid/v4');
    let uid = uuidv4().replace(/\-/g,"");

    // 构建model中需要展示的内容
    let smg = new StationMsgGroup();
    smg.setCategory(category);
    smg.setStationKey(classObj.selectedStationKey);
    let m = new Message();
    if(selectedStatus == true){
      m.setMsgTypeName(msgTypeName);
      m.setNodeKey(uid);
      m.setInputs(inputs);
      m.setStatus(true);
      m.setId(mtId);
      m.setCategory(category);
      smg.setMessage(m);
      let tmpMap = new Map();

      if(!stationMsgsBindedMap.has(classObj.selectedStationKey)){ 
        smg.setOperation("add");
      }
      else{
        tmpMap = stationMsgsBindedMap.get(classObj.selectedStationKey);
        let delNode;
        let tmpPO;
        if(tmpMap.has(category)){
          // 替换同一个category中的不同message type
          delNode = tmpMap.get(category);
        }
        else if(category.toUpperCase() == "PRODUCTID" || category.toUpperCase() == "ORDERID"){
            tmpPO = category.toUpperCase()=="PRODUCTID"?"ORDERID":"PRODUCTID";
            // 替换同一个category中的不同message type
            delNode = tmpMap.get(tmpPO);
        }

        if(typeof(delNode) != "undefined"){
          // 同一category下，去掉旧checkbox反选
          let oldMtId = delNode.mtId;
          classObj.cancelCategoryMtSelect(oldMtId);
          tmpMap.delete(tmpPO);

          // 旧Id
          smg.setOperation("update");
          smg.setNodeId(delNode.id);
        }
      }

      // 内存中保存，各category下message type
      // 每个category只能选择一个message type实例
      tmpMap.set(category, {"id":uid,"mtId":mtId});
      stationMsgsBindedMap.set(classObj.selectedStationKey, tmpMap);
    }
    else{
      // 从内存中移除
      let tmpMap = stationMsgsBindedMap.get(classObj.selectedStationKey);
      let delNode = tmpMap.get(category);
      smg.setOperation("delete");
      smg.setNodeId(delNode.id)

      tmpMap.delete(category);
    }

    // 发送到model
    this.msChange.emit(JSON.stringify(smg));
  }

  /**
   * 取消同一category下，旧MT勾选
   * @param mtId 
   */
  cancelCategoryMtSelect(mtId){
    for(let i=0;i < classObj.groupMessages.length;i++){
      let tmpMessages = classObj.groupMessages[i].messages;
      for(let j=0;j<tmpMessages.length;j++){
        if(mtId == tmpMessages[j].mtId){
          tmpMessages[j].status = false;
          return;
        }
      }
    }
  }

  ngOnInit() {
  }

  /**
   *获得该line上配置的message和勾选的变量
   * @param lineId
   */
  readStationMtsById(lineId){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/'+ lineId + '/messagetype';
    return this.http.get(url, options)
      .subscribe(
        response => {
          if(response['_body'] != ""){
            let pros = response.json();
            if(typeof(pros) != "undefined" && pros != null ){
              // 获得所有mt
              msgTypesStr = pros.messages;
              let mtMap = new Map();
              for(let j=0;j<msgTypesStr.length;j++){
                mtMap.set(msgTypesStr[j].mtId, msgTypesStr[j]);
              }

              // 将保存的关系转化为map
              stationMsgsBindedMap = new Map();
              let stationMtRlas = pros.lmrs;
              for(let i=0;i<stationMtRlas.length;i++){
                let tmpM = mtMap.get(stationMtRlas[i].mtId);
                
                if(!stationMsgsBindedMap.has(stationMtRlas[i].stationId)){
                  let tmpMap = new Map();
                  tmpMap.set(tmpM.category, {"id":stationMtRlas[i].mtUniqId,"mtId":stationMtRlas[i].mtId});
                  stationMsgsBindedMap.set(stationMtRlas[i].stationId, tmpMap);
                }
                else{
                  stationMsgsBindedMap.get(stationMtRlas[i].stationId).set(tmpM.category, {"id":stationMtRlas[i].mtMtUniqId,"mtId":stationMtRlas[i].mtId});
                }
              }

              this.convertStrToMsg(msgTypesStr, 0);
            }
          }
      },
      err => {
        console.log(err);
        },
      () => {
      }
      );
  }

  ngAfterViewInit() {
  }

  /**
   * 点击按钮上传文件
   */
  fileUpload(){
    let dialogRef = this.dialog.open(UploadDialogComponent, {
      disableClose: true,   //点击对话框外不消失
      hasBackdrop: true,
      height: '400px',
      width: '400px'
    });

    dialogRef.afterClosed().subscribe(value => {
      if(typeof(value) != "undefined" && value != null && value.length > 0){
          msgTypesStr = value;
          this.convertStrToMsg(value, 0);
      }
    });
  }
}

class Message{
  // message type自身Id
  private id:string;
  // gojs node key
  private nodeKey:string;
  private msgTypeName:string;
  private status:boolean;
  private disabled:boolean;
  private description:string;
  private inputs:string[];
  private options:string;
  private outputs:String[];
  //private changeOver:boolean;
  private category:string;

  constructor() {
    this.inputs = [];
    this.outputs = [];
    //this.changeOver = false;
    this.status = false;
    this.disabled = false;
  }

  setCategory(category){
    this.category = category;
  }

  setNodeKey(nodeKey){
    this.nodeKey = nodeKey;
  }

  setId(id){
    this.id = id;
  }

  setMsgTypeName(msgTypeName:string){
    this.msgTypeName = msgTypeName;
  }

  getMsgTypeName(){
    return this.msgTypeName;
  }

  getInputs():string[]{
    return this.inputs;
  }

  getOutputs():String[]{
    return this.outputs;
  }

  //getChangeOver():boolean{
  //  return this.changeOver;
  //}

  //setChangeOver(changeOver:boolean){
  //  this.changeOver = changeOver;
  //}

  getStatus():boolean{
    return this.status;
  }

  setStatus(status:boolean){
    this.status = status;
  }

  setInputs(inputs){
    this.inputs = inputs;
  }
}

class StationMsgGroup{
  private stationKey:string;
  private stationName:string;
  private message:Message;
  // add表示新增节点，update表示更新，delete表示删除
  private operate:string="add";
  // 更新和删除时，gojs model中需要删除节点的key
  private nodeId:string;
  // 选择的message type类型
  private category:string;

  setCategory(category){
    this.category = category;
  }

  setNodeId(nodeId){
    this.nodeId = nodeId;
  }

  setOperation(operate){
    this.operate = operate;
  }

  setStationKey(stationKey){
    this.stationKey = stationKey;
  }

  setMessage(message){
    this.message = message;
  }

  setStationName(stationName){
    this.stationName = stationName;
  }
}

class GroupMessage{
  private category:string;
  private display:boolean = true;
  private messages:Message[];

  setCategory(category:string){
    this.category = category;
  }

  setDisplay(display){
    this.display = display;
  }

  setMessages(messages){
    this.messages = messages;
  }
}

class Ele{
  private name:string;
  private id:string;
  private no:number;

  setName(name:string){
    this.name=name;
  }

  setNo(no:number){
    this.no=no;
  }

  setId(id:string){
    this.id=id;
  }

  getId(){
    return this.id;
  }

  getNo(){
    return this.no;
  }
  
  getName(){
    return this.name;
  }
}