import {
  PlcComponent
} from './plc/plc.component';
import {
  OpcuaComponent
} from './opcua/opcua.component';
export abstract class DeviceOperation {

  private ioDeviceId: string;
  private ioDeviceName: string = "";
  // 用于展示
  private displayName: string = "";
  // device的类型，如button、scanner等
  private type: string = "";
  private allDeviceIns = new Map();
  private protocols: any = new Array();
  private algorithms: any = new Array();
  private convertNameMap: any = new Map();

  private ipStrs: Map < string, number > = new Map();
  private addressStrs: Map < string, number > = new Map();

  private urlStrs: Map < string, number > = new Map();
  private nodeIdStrs: Map < string, number > = new Map();

  // 保存设备配置信息
  abstract saveInfo(): void;

  // 切换协议
  abstract onProtocolChange(obj): void;

  initAlgos() {
    let p = new Ptl();
    p.setName("Max");
    p.setNo(0);
    this.algorithms.push(p);

    p = new Ptl();
    p.setName("Min");
    p.setNo(1);
    this.algorithms.push(p);

    p = new Ptl();
    p.setName("Sum");
    p.setNo(2);
    this.algorithms.push(p);
  }

  initNameMap() {
    this.convertNameMap.set("OPC UA", "OPCUA");
    this.convertNameMap.set("Digital I/O", "DIO");
    this.convertNameMap.set("S7", "S7");
    this.convertNameMap.set("USB", "USB");
  }

  /**
   * 更新自动补全
   * @param componentIns
   */
  updateAutoCompletePlc(instance) {
    this.ipStrs.forEach((value, key, map) => {
      (instance as PlcComponent).getIpOptions().push(key);
    });
    this.addressStrs.forEach((value, key, map) => {
      (instance as PlcComponent).getAddressOptions().push(key);
    });
  }

  updateAutoCompleteOpcua(instance) {
    this.urlStrs.forEach((value, key, map) => {
      (instance as OpcuaComponent).getUrlOptions().push(key);
    });
    this.nodeIdStrs.forEach((value, key, map) => {
      (instance as OpcuaComponent).getNodeIdOptions().push(key);
    });
  }

  getProtocols(): Array < Ptl > {
    return this.protocols;
  }

  getAlgos(): Array < Ptl > {
    return this.algorithms;
  }

  getConvertNameMap(): Map < string, string > {
    return this.convertNameMap;
  }

  getIpStrs() {
    return this.ipStrs;
  }

  getAddressStrs() {
    return this.addressStrs;
  }

  getUrlStrs() {
    return this.urlStrs;
  }

  getNodeIdsStrs() {
    return this.nodeIdStrs;
  }

  setIpStrs(ipStrs: Map < string, number > ) {
    this.ipStrs = ipStrs;
  }

  setAddressStrs(addressStrs: Map < string, number > ) {
    this.addressStrs = addressStrs;
  }

  setUrlStrs(urlStrs: Map < string, number > ) {
    this.urlStrs = urlStrs;
  }

  setNodeIdStrs(nodeIdStrs: Map < string, number > ) {
    this.nodeIdStrs = nodeIdStrs;
  }

  setIoDeviceId(ioDeviceId: string) {
    this.ioDeviceId = ioDeviceId;
  }

  getIoDeviceId() {
    return this.ioDeviceId;
  }

  setIoDeviceName(ioDeviceName: string) {
    this.ioDeviceName = ioDeviceName;
  }

  getIoDeviceName() {
    return this.ioDeviceName;
  }

  setAlias(displayName: string) {
    this.displayName = displayName;
  }

  getAlias() {
    return this.displayName;
  }

  setType(type: string){
    this.type = type;
  }

  getType(){
    return this.type;
  }

  setAllDeviceIns(allDeviceIns) {
    this.allDeviceIns = allDeviceIns;
  }

  getAllDeviceIns(){
    return this.allDeviceIns;
  }

  copyToAlias() {
    if (this.ioDeviceName != "") {
      this.displayName = this.ioDeviceName;
    }
  }
}

export class Ptl {
  private name: string;
  private no: number;

  getName(): string {
    return this.name;
  }

  setName(name: string) {
    this.name = name;
  }

  setNo(no: number) {
    this.no = no;
  }

  getNo(): number {
    return this.no;
  }
}
