import { Component, OnInit, Input } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { config } from '../configuration';

var classObj;

@Component({
  selector: 'app-publish',
  templateUrl: './publish.component.html',
  styleUrls: ['./publish.component.css']
})
export class PublishComponent implements OnInit {

    // 保存从父组件传递过来的lineId
    private _lineId: string = "";
    private plantName: string = "";
    private lineName: string = "";
    private boxSerialNumber: string = "";
    private publishStatus: string = "Not Published";
  
    // 父组件传递line id获得该line下的配置
    @Input()
    set lineId(id:string){
      this._lineId = id;
      if(id != ""){
        this.readPublishInfoById(this._lineId);
      }
    }

  constructor(private http: Http) { 
    classObj = this;
  }

  ngOnInit() {
  }

  readPublishInfoById(lineId){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/lines/' + lineId + '/publishCfg';
    return this.http.get(url, options)
      .subscribe(
        response => {
          if(response['_body'] != ""){
            let pros = response.json();
            if(typeof(pros) != "undefined" && pros != null ){
              classObj.plantName = pros.plantName;
              classObj.lineName = pros.lineName;
              classObj.boxSerialNumber = pros.boxSerialNumber;
            }
          }
        },
        err => {
        },
        () => console.log('Call Complete')
      );
  }

}
