import { Component, OnInit,Output,EventEmitter,ViewChild,ElementRef} from '@angular/core';

declare var $: any;

@Component({
  selector: 'app-button',
  templateUrl: './io.component.html',
  styleUrls: ['./io.component.css']
})
export class IoComponent implements OnInit {

  //private name: string="";
  //private alias: string="";
  private pin: string="";
  private cycles: any = new Array();
  private dataTypes: any = new Array();
  private dirs: any = new Array();
  private triggers: any = new Array();
  private selectedCycle: string;
  private selectedDataType: string;
  private selectedDir: string;
  private selectedTrigger: string;

  constructor() {
    this.selectedCycle = "5s";
    this.selectedDataType = "INT";
    this.selectedDir="DIR_OUT";
    this.selectedTrigger="EDGE_RISING";

    this.cycles.push("10ms");
    this.cycles.push("100ms");
    this.cycles.push("500ms");
    this.cycles.push("1s");
    this.cycles.push("5s");
    this.cycles.push("10s");
    this.cycles.push("15s");
    this.cycles.push("30s");
    this.cycles.push("1m");

    this.dataTypes.push("BOOLEAN");
    this.dataTypes.push("INT");
    this.dataTypes.push("LONG");
    this.dataTypes.push("DOUBLE");
    this.dataTypes.push("STRING");

    this.dirs.push("DIR_OUT");
    this.dirs.push("DIR_IN");
    this.dirs.push("DIR_OUT_HIGH");
    this.dirs.push("DIR_OUT_LOW");

    this.triggers.push("EDGE_NONE");
    this.triggers.push("EDGE_BOTH");
    this.triggers.push("EDGE_RISING");
    this.triggers.push("EDGE_FALLING");
  }

  ngOnInit() {
  }

  //setName(name:string){
  //  this.name = name;
  //}

  //setAlias(alias){
  //  this.alias = alias;
  //}

  setPin(pin:string){
    this.pin = pin;
  }

  setSelectedCycle(cycle:string){
    this.selectedCycle = cycle;
  }

  setSelectedDataType(dataType:string){
    this.selectedDataType = dataType;
  }

  setSelectedDir(dir:string){
    this.selectedDir = dir;
  }

  setSelectedTrigger(trigger:string){
    this.selectedTrigger = trigger;
  }

  onCycleChange(newVal)
  {
    this.selectedCycle = newVal;
  }

  onDataTypeChange(newVal)
  {
    this.selectedDataType = newVal;
  }

  onDirChange(newVal){
    this.selectedDir = newVal;
  }

  onTriggerChange(newVal){
    this.selectedTrigger = newVal;
  }

  cfgComponentInfo(nodeObj){
    //this.setName(nodeObj.name);
    //this.setAlias(nodeObj.alias);
    let cfg;
    if(typeof(nodeObj.config)=="string"){
      cfg = JSON.parse(nodeObj.config);
    }
    else{
      cfg = nodeObj.config;
    }
    this.setPin(cfg.pin);
    this.setSelectedCycle(cfg.cycle);
    this.setSelectedDataType(cfg.dataType);
    this.setSelectedDir(cfg.dir);
    this.setSelectedTrigger(cfg.trigger);
  }

  //copyToAlias(){
  //  if(this.name != ""){
  //    this.alias = this.name;
  //  }
  //}
  
  saveComponentInfo():IoInfo{
    if(this.judgeInputNull(this.pin.trim())){
        alert("All properties need to be filled!");
        return;
      }
      else{
        let ii = new IoInfo();
        //ii.setName(this.name.trim());
        //ii.setAlias(this.alias.trim());
        ii.setPin(this.pin.trim());
        ii.setProtocol("DIO");
        if(this.selectedCycle != null && this.selectedCycle != ""){
          ii.setCycle(this.selectedCycle);
        }
        if(this.selectedDataType != null && this.selectedDataType != ""){
          ii.setDataType(this.selectedDataType);
        }
        if(this.selectedDir != null && this.selectedDir != ""){
          ii.setDir(this.selectedDir);
        }
        if(this.selectedTrigger != null && this.selectedTrigger != ""){
          ii.setTrigger(this.selectedTrigger);
        }
        return ii;
      }
  }

  judgeInputNull(input:any) :boolean{
    if(typeof(input)==undefined || input == null || input.trim() == ""){
      return true;
    }
    else{
      return false;
    }
  }
}

export class IoInfo{
  //private name: string;
  //private alias: string;
  private pin: string;
  private protocol: string;
  private cycle:string;
  private dataType:string;
  private dir:string;
  private trigger:string;

  constructor(){
    //this.name="";
    //this.alias="";
    this.pin="";
    this.cycle="5s";
    this.dataType="INT";
    this.dir="DIR_OUT";
    this.trigger="EDGE_RISING";
  }

  //setName(name:string){
   // this.name = name;
  //}

  //getName(): string{
  //  return this.name;
  //}

  //setAlias(alias){
  //  this.alias=alias;
  //}

  //getAlias(){
  //  return this.alias;
  //}

  setPin(pin:string){
    this.pin = pin;
  }

  getPin(): string{
    return this.pin;
  }

  setProtocol(protocol:string){
    this.protocol = protocol;
  }

  setCycle(cycle:string){
    this.cycle = cycle;
  }

  getCycle(){
    return this.cycle;
  }

  setDataType(dataType:string){
    this.dataType = dataType;
  }

  getDataType(){
    return this.dataType;
  }

  setDir(dir:string){
    this.dir=dir;
  }

  getDir(){
    return this.dir;
  }

  setTrigger(trigger:string){
    this.trigger=trigger;
  }

  getTrigger(){
    return this.trigger;
  }
}
