import { Component, OnInit } from '@angular/core';
import { ProjectService } from '../project.service';
import { ProductCfgComponent,Product } from '../product-cfg/product-cfg.component';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { Http, Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { config } from '../configuration';
import { OptconfirmdialogComponent } from '../optconfirmdialog/optconfirmdialog.component';

@Component({
  selector: 'app-product-mgt',
  templateUrl: './product-mgt.component.html',
  styleUrls: ['./product-mgt.component.css']
})
export class ProductMgtComponent implements OnInit {

  private products: Product[] = [];
  private createProductIcon: string;
  private editIcon:string;
  private deleteIcon:string;

  private proMap: Map<string,Product> = new Map();

  constructor(private projectService: ProjectService, public dialog: MatDialog, private http: Http) {
    this.createProductIcon = config.iconPosition+"circle_add.png";
    this.editIcon = config.iconPosition+"edit.png";
    this.deleteIcon = config.iconPosition+"delete.png";
  }

  ngOnInit() {
    this.readAllProducts();
  }

  ngAfterViewInit() {
  }

  readAllProducts(){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/products/';
    return this.http.get(url, options)
      .subscribe(
        response => {
           let pros = response.json();
           if(typeof(pros) != "undefined" && pros != null && pros.length > 0){
             for(let i=0; i<pros.length;i++){
              let p = new Product();
              p.setName(pros[i].name);
              p.setId(pros[i].id);
              p.setDescription(pros[i].description);
              this.products.push(p);
              this.proMap.set(pros[i].id, p);
             }
           }
          },
        err => {
          },
        () => console.log('Call Complete')
      );
  }

  /**
   * 创建product
   */
  createProduct(){
    let dialogRef = this.dialog.open(ProductCfgComponent, {
      data: {
        id:'',
        operate:'new',
        name:'',
        comment:''
      },
      hasBackdrop: true,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result != "" && typeof(result) != "undefined")
      {
        let pro = new Product();
        pro.setName(result.name);
        pro.setDescription(result.description);
        pro.setId(result.id);

        this.products.push(pro);
        this.proMap.set(result.id,pro);
      }
    });
  }

  /**
   * 修改product配置
   * @param cfgInfo
   */
  modifyProductCfg(id:any){
    let pro = this.proMap.get(id);
    let dialogRef = this.dialog.open(ProductCfgComponent, {
       data: {
         id:id,
         operate:'modify',
         name:pro.getName(),
         comment:pro.getDescription()
       },
       hasBackdrop: true,
       disableClose: true
     });

     dialogRef.afterClosed().subscribe(result => {
       if(result != "" && typeof(result) != "undefined"){
        var id = result.id;
        for(let i=0;i<this.products.length;i++){
          let tmpId = this.products[i].getId();
          if(id == tmpId){
            this.products[i].setName(result.name);
            this.products[i].setDescription(result.description);
            this.proMap.set(result.id,this.products[i]);
            break;
          }
        }
       }
     });
  }

  /**
   * 删除product
   * @param id
   */
  deleteProduct(id:any){
    let dialogRef = this.dialog.open(OptconfirmdialogComponent, {
      data: {"hintMgs":"Are you sure to delete?"},
      hasBackdrop: true,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if("confirm" == result){
        this.proMap.delete(id);
        for(let i=0;i<this.products.length;i++){
          let tmpId = this.products[i].getId();
          if(id == tmpId){
            this.products.splice(i,1);
            break;
          }
        }

        this.delProductDB(id);
      }
    });
  }

  delProductDB(id:string){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/products/' + id;
    return this.http.delete(url, options)
      .subscribe(
        response => {

          },
        err => {
          },
        () => {alert('Delete success!')}
      );
  }

  jumpCreateProduct(obj){
    let productName = obj.currentTarget.text;
    let productId = obj.currentTarget.id;
    this.projectService.createLineNetwork(productName,productId);
  }

}
