import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import * as go from 'gojs';
import { Input, Output, EventEmitter} from '@angular/core';
import { HintdialogComponent } from '../hintdialog/hintdialog.component';
import { Http, Headers, RequestOptions, URLSearchParams} from '@angular/http';
import { MatDialog, MatDialogConfig} from '@angular/material';
import { Router,ActivatedRoute } from '@angular/router';
import { config } from '../configuration';

declare var $: any;
declare var require:any;

var classObj;

var lineCountMap;

const uuidv4 = require('uuid/v4');

@Component({
  selector: 'app-line-network',
  templateUrl: './line-network.component.html',
  styleUrls: ['./line-network.component.css']
})
export class LineNetworkComponent implements OnInit {

  // url 传递过来的参数
  private productName: any;
  private productId: any;

  private diagram: go.Diagram = new go.Diagram();

  // 模型div
  @ViewChild('modelBlock')
  private diagramRef: ElementRef;

  @Input()
  get model(): go.Model {
    return this.diagram.model;
  }
  set model(val: go.Model) {
    this.diagram.model = val;
  }

  @Output()
  nodeSelected = new EventEmitter < go.Node | null > ();

  @Output()
  modelChanged = new EventEmitter < go.ChangedEvent > ();

  constructor(private dialog: MatDialog,private http: Http,private activeRoute: ActivatedRoute) {

    classObj = this;
    lineCountMap = new Map();

    const $ = go.GraphObject.make;
    this.diagram = new go.Diagram();
    this.diagram.initialContentAlignment = go.Spot.Center;
    this.diagram.toolManager.mouseWheelBehavior = go.ToolManager.WheelNone, //鼠标滚轮事件禁止
    this.diagram.allowDrop = true; // necessary for dragging from Palette
    //this.diagram.model.makeUniqueKeyFunction = this.virtualUniqueKey;
    this.diagram.undoManager.isEnabled = true;
    	
    //添加监听节点生成事件
    this.diagram.addDiagramListener("externalobjectsdropped", function(e) {
      let lineId;

      // 获得点击节点的键
      e.subject.each(n => {
        lineId = n.data.id;
      });

      if(lineCountMap.has(lineId)){
        let num = lineCountMap.get(lineId);
        num++;
        lineCountMap.set(lineId,num);
      }
      else{
        lineCountMap.set(lineId, 1);
      }
    })

    this.diagram.addDiagramListener("SelectionDeleted",
      e => {
        e.subject.each(function(n){
          // 如果是节点不是线
          if(n instanceof go.Node){
            let lineId = n.data.id;

            // 更新line的map
            let num = lineCountMap.get(lineId);
            num--;
            if(num > 0){
              lineCountMap.set(lineId,num);
            }
            else{
              lineCountMap.delete(lineId);
            }
          }
     });
    });

    this.diagram.addDiagramListener("ChangedSelection",
      e => {
        const node = e.diagram.selection.first();
        this.nodeSelected.emit(node instanceof go.Node ? node : null);
        //    console.log(node.location.x);
    });

    this.diagram.addModelChangedListener(e => e.isTransactionFinished && this.modelChanged.emit(e));

    this.diagram.nodeTemplate =
      $(go.Node, "Auto",
        { resizable: true },
        new go.Binding("desiredSize", "size", go.Size.parse).makeTwoWay(go.Size.stringify),
        new go.Binding("location", "loc", go.Point.parse).makeTwoWay(go.Point.stringify),
        $(go.Panel, "Auto", {
            name: "BODY"
          },
          $(go.Shape, "RoundedRectangle", {
              fill: "#ADBECB",
              stroke: "black",
              strokeWidth: 2,
              portId: "",
              cursor: "pointer",
              // allow many kinds of links
              fromLinkable: true,
              toLinkable: true,
              fromLinkableSelfNode: true,
              toLinkableSelfNode: true,
              fromLinkableDuplicates: true,
              toLinkableDuplicates: true
            },
            new go.Binding("fill", "color")),
          $(go.Panel, "Horizontal", {
              margin: 1
            },
            $(go.TextBlock, {
                margin: 10,
                editable: false,
                font: "bold 15px sans-serif"
              },
              new go.Binding("text").makeTwoWay()),
              $(go.TextBlock, {
                stroke: "0xE8E8E8",
                margin: 2,
                editable: false,
                font: "12px sans-serif"
              },
              new go.Binding("text","category"))),
          )
      );
  }

  ngOnInit() {
    this.diagram.div = this.diagramRef.nativeElement;

    // 获得url传递的参数
    this.activeRoute.queryParams.subscribe(params => {
      this.productName = params['productName'];
      this.productId = params['productId'];
    });

    this.readProductModelById(this.productId);
  }

  ngAfterViewInit() {
    this.initMainPanelHeight();
  }

  /**
   * 调节各模块的大小
   */
  initMainPanelHeight() {
    var bodyHeight = $("body").height();
    var divH = bodyHeight - 64;
    $("#mainPanel").height(divH);
  }

  /**
   * 代理解决跨域问题
   */
  publishModelData() {
    this.saveTimeToDB();
    this.saveModelData("publish");
    var responseData = this.productId;
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(config.publishServer+'/config/publish/product', responseData, options)
      .subscribe(
        data => {
          let pubStatus = 'Publish ' + data.statusText;
          let dialogRef = classObj.dialog.open(HintdialogComponent, {
            data: {"statusText": pubStatus},
            hasBackdrop: true,
            disableClose: true,
            width: "200px"
          });},
        err => {
          let dialogRef = classObj.dialog.open(HintdialogComponent, {
            data: {"statusText":"Publish ERROR"},
            hasBackdrop: true,
            disableClose: true,
            width: "200px"
          });},
        () => console.log('Call Complete')
      );
  }

  saveTimeToDB(){
    var responseData = "save time";
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.publishServer + '/time/productUpdate';
    return this.http.post(url, responseData, options)
      .subscribe(
        data => {

          },
        err => {
          },
        () => console.log("Update time success!")
      );
  }

  saveModelData(operation) {
    var responseData = this.diagram.model.toJson();
    let p = new ProductModel();
    p.setId(this.productId);
    p.setModel(responseData);

    // map 转化为对象数组
    let arr = [];
    lineCountMap.forEach((value, key, map) => {
      let l = new LineNum();
      l.setLineId(key);
      l.setCount(value);
      arr.push(l);
    });
    p.setLines(JSON.stringify(arr));
    this.saveProductModelToDB(p,operation);
  }

  saveProductModelToDB(product,operation) {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/products/' + product.id + '/model';
    return this.http.post(url, product, options)
      .subscribe(
        data => {

          },
        err => {
          },
        () => {
          if("save" == operation)
            alert("Save success!");
          }
      );
  }

  readProductModelById(id:string){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    let url = config.backendUrl + '/products/' + id + '/model';
    return this.http.get(url, options)
      .subscribe(
        response => {
          if(response['_body'] != ""){
            let pros = response.json();
            if(typeof(pros) != "undefined" && pros != null ){
              classObj.diagram.model = go.Model.fromJson(pros.model);

              if(typeof(pros.lines) != "undefined" 
              && pros.lines != null && pros.lines != ""){
                let lines = JSON.parse(pros.lines);
                for(let i=0;i<lines.length;i++){
                  lineCountMap.set(lines[i].lineId, lines[i].count);
                }
              }
            }
          }
          },
        err => {
          },
        () => console.log('Call Complete')
      );
  }

}

class ProductModel{
  id:string;
  model:string;
  lines:string;

  setId(id:string){
    this.id = id;
  }

  setModel(model:string){
    this.model = model;
  }
  
  setLines(lines:string){
    this.lines = lines;
  }
}

class LineNum{
  lineId:string;
  count:number;

  setLineId(lineId:string){
    this.lineId = lineId;
  }

  setCount(count:number){
    this.count = count;
  }
}
